import {
    UPDATE_USER,
    SET_USER_DATA,
    REMOVE_USER,
    OPEN_ENTER_FORM,
    CLOSE_ENTER_FORM,
    ADD_COMPANY,
    SET_COMPANIES,
    ADD_FACTORY
} from "../types";

const initialState = {
    openEnterForm: false,
    token: "",
    companyToken: "",
    username: "",
    first_name: "",
    last_name: "",
    middle_name: "",
    email: "",
    change_password_date: undefined,
    city: undefined,
    country: undefined,
    date_of_birth: undefined,
    id: 0,
    is_active: true,
    is_staff: true,
    is_superuser: true,
    need_change_password: false,
    organization_account: false,
    telegram_confirmed: false,
    telephone: null,
    loading: true,
    company: undefined,
    companies: [],
    factories: [],
};

export const UserReducer = (state = initialState, action) => {
    // console.log("PAYLOAD:", action.payload)
    switch (action.type) {
        case SET_USER_DATA:
            return {
                ...state,
                ...action.payload,
                loading: false,
            };
        case UPDATE_USER:
            return {
                ...state,
                ...action.payload,
                // loading: false,
            };
        case REMOVE_USER:
            return {
                ...state,
                name: "",
                userName: "",
                password: "",
                email: "",
            }
        case OPEN_ENTER_FORM:
            return {
                ...state,
                openEnterForm: true,
            }
        case CLOSE_ENTER_FORM:
            return {
                ...state,
                openEnterForm: false,
            }
        case ADD_COMPANY:
            return {
                ...state,
                companies: [...state.companies.filter(c => c.id !== action.payload.id), action.payload],
            }
        case ADD_FACTORY:
            return {
                ...state,
                factories: [...state.factories.filter(f => f.id !== action.payload.id), action.payload],
            }
        case SET_COMPANIES:
            // console.log("PAYLOAD:", action.payload)
            return {
                ...state,
                companies: action.payload,
            }
        default:
            return state;
    }
};
