import React, {useEffect, useRef, useState} from 'react';
import styles from "./styles.module.scss";
import RequestWrapper from "../../helpers/RequestWrapper";
import AppButton from "../../components/ui/AppButton";
import AppPreviewProduct from "../../components/ui/AppPreviewProduct";
import {useDispatch} from "react-redux";
import {falseLoadingForUserData, waitingForUserData} from "../../store/actions/user";
import {MAIN_URL, SEARCH_URL} from "../../constants";

const NAMES_REQUEST = "products/search/products/name/"
const PRODUCTS_REQUEST = "products/search/products/"

const OFFSET_SHIFT = 10;

function SearchPage({history}) {

    // function Right (index) {
    //     document.getElementById(index.toString()).classList.add(styles.right);
    //     document.getElementById(index.toString()).classList.remove(styles.wrong)
    // }
    //
    // function Wrong (index) {
    //     document.getElementById(index.toString()).classList.remove(styles.right);
    //     document.getElementById(index.toString()).classList.add(styles.wrong)
    // }

    const [q, setQ] = useState("")
    const [offset, _setOffset] = useState(0)
    const offsetRef = useRef(offset)
    const setOffset = (v) => {
        offsetRef.current = v
        _setOffset(v);
    }
    const [left, setLeft] = useState(0)
    const [total, setTotal] = useState(0)
    const [names, setNames] = useState([])
    const [products, setProducts] = useState([])
    const nameRequestTimeout = useRef(undefined)
    const [searchQ, setSearchQ] = useState("")

    const {REQUEST} = RequestWrapper();

    const onNameRequest = () => {
        REQUEST.GET({request: `${NAMES_REQUEST}?q=${q}`}).then(res => {
            if (res.status === 200) {
                setNames(res.json.map(name => ({value: name})))
            }
        })
    }

    useEffect(() => {
        clearTimeout(nameRequestTimeout.current)
        nameRequestTimeout.current = setTimeout(onNameRequest, 700)

        return () => clearTimeout(nameRequestTimeout.current)
    }, [q])

    const dispatch = useDispatch();


    const onSearchRequest = () => {
        let p = products
        if (q !== searchQ) {
            p = [];
            setProducts([])
        }
        setSearchQ(q)
        if (q.length === 0) {
            return;
        }
        dispatch(waitingForUserData())
        REQUEST.GET({request: `${PRODUCTS_REQUEST}?q=${q}&offset=${offsetRef.current}`}).then(res => {
            console.log("SEARCH Q:", res)
            dispatch(falseLoadingForUserData())
            if (res.status === 200) {
                setProducts([...p, ...res.json.products])
                setLeft(res.json.left)
                setTotal(res.json.total)
            }
        })
    }

    const onInputKeyDown = (e) => {
        if (e.key === 'Enter') {
            onSearchRequest()
        }
    }

    const onProduct = (id) => {
        console.log("PATH PRODUCT:", MAIN_URL.SEARCH + SEARCH_URL.PRODUCT + `/${id}`)
        history.push(MAIN_URL.SEARCH + SEARCH_URL.PRODUCT + `/${id}`)
    }

    const onAddProducts = () => {
        setOffset(offsetRef.current + OFFSET_SHIFT)
        onSearchRequest();
    }

    return <div className={styles.container}>
        <div className={styles.header}>
            <input
                className={styles.search_input}
                type={"text"}
                list={"search_list"}
                name={"search"}
                onKeyDown={onInputKeyDown}
                placeholder={"  ..."}
                value={q} onChange={e => {
                // console.log(e.target)
                setQ(e.target.value)
            }}/>
            <datalist id="search_list">
                {names.map((item, index) => <option
                    className={styles.search_option}
                    key={index.toString()} {...item}/>)}
            </datalist>
            <AppButton inverse onClick={onSearchRequest}>
                
            </AppButton>
        </div>
        <div className={styles.main_container}>
            {searchQ.length > 0 ? (
                products.length > 0 ?
                    <div className={styles.products_container}>
                        {products.map((product, index) =>
                            <AppPreviewProduct
                                onClick={() => onProduct(product.id)}
                                key={product.id.toString()} {...product}/>)}
                    </div>
                    : <h4>
                         ,    .
                                 .
                    </h4>
            ) : <h4>
                      
            </h4>}
        </div>
        <div className={styles.button_add_container}>
            {left > 0 && <AppButton onClick={onAddProducts}>
                 
            </AppButton>}
        </div>
    </div>
}

export default SearchPage;
