import React, {useEffect, useState} from 'react';
import RequestView from "../../../../components/RequestView";
import AppInputShow from "../../../../components/ui/AppInputShow";
import AppPreviewProduct from "../../../../components/ui/AppPreviewProduct";
import AppButton from "../../../../components/ui/AppButton";
import RequestWrapper from "../../../../helpers/RequestWrapper";
import styles from "./styles.module.scss";

const GET_RECEIVED_LIST = "orders/receive/list/active/"
const REJECT_REQUEST = "orders/request/reject/"
const ACCEPT_REQUEST = "orders/request/accept/"


const WAITING_STATUS = 1;

const GetStatus = (status) => {
    switch (status) {
        case 0:
            return " "
        case 2:
            return " .    ."
        default:
            return " ."
    }
}


function CompanyReceivedRequestPage({history, match}) {

    // useEffect(() => {
    //     document.title = " | ";
    // }, [])

    const [loading, setLoading] = useState(true)
    const companyId = match.params.company;
    const [list, setList] = useState([])
    const {REQUEST} = RequestWrapper();

    const onRequest = (res) => {
        if (typeof res.json.list === 'object') {
            setList(res.json.list)
            setLoading(false)
        }
    }

    const onAccept = (id) => {
        REQUEST.POST({
            request: ACCEPT_REQUEST,
            body: {
                id: id,
                company_executor: companyId,
            },
            onSuccess: {message: " .   ."}
        }).then(res => {
            if (res.status === 200) {
                setList(l => l.map(item => {
                    if (item.product_request_id === id) {
                        item.status = 2
                    }
                    return item;
                }))
            }
        })
    }

    const onReject = (id) => {
        REQUEST.POST({
            request: REJECT_REQUEST,
            body: {
                id: id,
                company_executor: companyId,
            },
            onSuccess: {message: " ."}
        }).then(res => {
            if (res.status === 200) {
                setList(l => l.map(item => {
                    if (item.product_request_id === id) {
                        item.status = 0
                    }
                    return item;
                }))
            }
        })
    }

    return <RequestView
        request={`${GET_RECEIVED_LIST}?company=${companyId}`}
        loading={loading}
        onRequest={onRequest}
    >
        {list.length > 0 ? list.map((item) =>
            <div
                className={styles.request_container}
                key={item.product_request_id.toString()}>
                <AppInputShow
                    label={" "}
                    value={item.product_request_id}/>
                <AppInputShow
                    label={":"}
                    value={GetStatus(item.status)}/>
                <AppInputShow
                    label={" :"}
                    value={item.amount}/>
                {item.product && <AppPreviewProduct inverse {...item.product}/>}
                {item.status === WAITING_STATUS && <div className={styles.buttons}>
                    <AppButton type={"green"} onClick={() => onAccept(item.product_request_id)}>
                         
                    </AppButton>
                    <AppButton type={"red"} onClick={() => onReject(item.product_request_id)}>
                         
                    </AppButton>
                </div>}
            </div>) : <h4>
                  .
        </h4>}
    </RequestView>
}

export default CompanyReceivedRequestPage;
