import React, {useEffect, useState} from 'react';
// import styles from "./styles.module.scss";
import {falseLoadingForUserData, waitingForUserData} from "../../../store/actions/user";
import {useDispatch} from "react-redux";
import RequestWrapper from "../../../helpers/RequestWrapper";
import {COMPANY_URL, FACTORY_URL, MAIN_URL, ORGANIZATIONS_URL, SEARCH_URL} from "../../../constants";
import RequestView from "../../../components/RequestView";
import CreateProductRequestForm from "../../../containers/Forms/CreateProductRequestForm";

// const GET_REQUEST = "organizations/factory/get/"
const GET_COMPANIES = "organizations/company/list/"
const GET_PRODUCT_PREVIEW = "products/product/get/preview/"


function CreateProductRequestPage(
    {
        history,
        match
    }) {

    const productId = match.params.product;

    const onAdd = () => {
        history.push(MAIN_URL.SEARCH + SEARCH_URL.PRODUCT + `/${productId}`)
    }

    const [companies, setCompanies] = useState([]);
    const [loading, setLoading] = useState(true);
    const [name, setName] = useState("");

    const onRequest = (res) => {
        setCompanies(res.json.company_list)
        // setName(res.json.info.name)
        setLoading(false)
    }

    const {REQUEST} = RequestWrapper();
    const dispatch = useDispatch();

    useEffect(() => {
        REQUEST.GET({request: `${GET_PRODUCT_PREVIEW}?id=${productId}`}).then(res => {
            if (res.status === 200) {
                setName(res.json.name)
            }
        })
    }, [productId])

    const [errors, setErrors] = useState({})
    const [error, setError] = useState("")

    const onCreateRequest = (body) => {
        dispatch(waitingForUserData())
        REQUEST.POST({
            request: "orders/request/create/",
            body: {...body, product: productId, request: "-"},
            onSuccess: {message: " .     ."}
        }).then(res => {
            dispatch(falseLoadingForUserData())
            try {
                console.log("|>>>>> NEW STAFF USERS:", res.json)
                if (res.status === 200) {
                    onAdd()
                    // setUsers(res.json)
                } else {
                    setErrors(res.json)
                }
            } catch (e) {
                setError(":" + e.toString())
                console.log("ERR GET COMPANY:", e);
            }
        })
    };

    return <RequestView
        request={`${GET_COMPANIES}`}
        loading={loading}
        onRequest={onRequest}
    >
        {typeof companies === 'object' && companies.length > 0 ?
            <CreateProductRequestForm
                onSubmit={onCreateRequest}
                requestErrors={errors}
                error={error}
                name={name}
                companies={companies}
            /> :
            <h4>
                     ,      -
                .
            </h4>}
    </RequestView>
}

export default CreateProductRequestPage;
