import React, {useEffect, useState} from 'react';
import RequestView from "../../../../components/RequestView";
import AppPreviewProduct from "../../../../components/ui/AppPreviewProduct";
import AppInputShow from "../../../../components/ui/AppInputShow";
import styles from "./styles.module.scss";

const GET_CREATED_LIST = "orders/created/list/active/"


const GetStatus = (status) => {
    switch (status) {
        case 0:
            return "   "
        case 2:
            return "  .   "
        default:
            return "  "
    }
}


function CompanyCreatedRequestsPage({history, match}) {

    // useEffect(() => {
    //     document.title = " | ";
    // }, [])

    const [loading, setLoading] = useState(true)
    const companyId = match.params.company;
    const [list, setList] = useState([])

    const onRequest = (res) => {
        if (typeof res.json.list === 'object') {
            setList(res.json.list)
            setLoading(false)
        }
    }

    return <RequestView
        request={`${GET_CREATED_LIST}?company=${companyId}`}
        loading={loading}
        onRequest={onRequest}
    >
        {list.length > 0 ? list.map((item) =>
                <div
                    className={styles.request_container}
                    key={item.id.toString()}>
                    <AppInputShow
                        label={" "}
                        value={item.id}/>
                    {/*<div>*/}
                    {/*    <p> </p>*/}
                    {/*    <h6>{item.id}</h6>*/}
                    {/*</div>*/}
                    <AppInputShow
                        label={":"}
                        value={GetStatus(item.status)}/>
                    {item.contacts && <AppInputShow
                        label={":"}
                        wrapperValue={"div"}
                        value={item.contacts.map((company, ic) =>
                            <div
                                key={ic.toString()}
                            >
                            <h6>{company.name}</h6>
                            <p>Email: {company.email ? company.email : " "}</p>
                            <h6>  :</h6>
                            {company.users.map((user, iu) =>
                                <div
                                    className={styles.user_contacts}
                                    key={iu.toString()}
                                >
                                <p>{user.full_name}</p>
                                {user.telephone && <p>: {user.telephone}</p>}
                                {user.email && <p>Email: {user.email}</p>}
                            </div>)}
                        </div>)}/>}
                    {/*<div>*/}
                    {/*    <p>:</p>*/}
                    {/*    <h6>  </h6>*/}
                    {/*</div>*/}
                    {/*<div>*/}
                    {/*    <p> :</p>*/}
                    {/*    <h6>{item.amount}</h6>*/}
                    {/*</div>*/}
                    <AppInputShow
                        label={" :"}
                        value={item.amount}/>
                    {item.product && <AppPreviewProduct inverse {...item.product}/>}
        </div>) : <h4>
                  .
        </h4>}
    </RequestView>
}

export default CompanyCreatedRequestsPage;
