import React, {useCallback, useEffect, useRef, useState} from 'react';
import styles from "./styles.module.scss";
import AppButton from "../../../components/ui/AppButton";
import AppInput from "../../../components/ui/AppInput";
import {AppWhiteBlock} from "../../../components/ui/AppWhiteBlock";
import FormPattern from "../FormPattern";
import RequestWrapper from "../../../helpers/RequestWrapper";
import {falseLoadingForUserData, waitingForUserData} from "../../../store/actions/user";
import {COMPANY_TYPE_LIST, COMPANY_URL, MAIN_URL, ORGANIZATIONS_URL} from "../../../constants";
import AppSelectedList from "../../../components/ui/AppSelectedList";
import {useDispatch} from "react-redux";
import AppInputWrapper from "../../../components/ui/AppInputWrapper";
import {useForm} from "react-hook-form";


function CompanyInformationForm(
    {
        onSubmit = () => {
        },
        editable = true,
        data = undefined,
        requestErrors = {},
        error = "",
        headerText = " ",
        buttonText = "",
    }) {

    const {
        register, handleSubmit, setError,
        setValue, formState: {errors, isValid, isDirty}
    } = useForm();

    useEffect(() => {
        if (typeof requestErrors === 'object') {
            for (let key in requestErrors) {
                setError(key, {
                    type: 'custom',
                    message: requestErrors[key]
                })
            }
        }
    }, [requestErrors])

    useEffect(() => {
        for (let key in data) {
            setValue(key, data[key]);
        }
    }, [data])

    const onLocalSubmit = useCallback((data) => {
        console.log("LOCAL SUBMIT:", data)
        onSubmit(data)
    }, [onSubmit])

    const [totalError, setTotalError] = useState(error)

    useEffect(() => {
        if (error) {
            setTotalError(error)
            return
        }
        const hasKeys = !!Object.keys(requestErrors).length;
        if (hasKeys) {
            setTotalError("   ")
        } else {
            setTotalError("")
        }
    }, [requestErrors, error])

    const dataArray = [
        {
            name: "name",
            type: "text",
            label: "",
            options: {required: " "},
            placeholder: "  ...",
            error: errors.name,
        },
        {
            name: "email",
            type: "text",
            label: "Email",
            // options: { required: " " },
            placeholder: " email ...",
            error: errors.email,
        },
        {
            name: "address",
            type: "text",
            label: " ",
            placeholder: " ...",
            error: errors.address,
        },
        {
            name: "inn",
            type: "text",
            label: "",
            placeholder: " ...",
            error: errors.inn,
        },
        {
            name: "postal_code",
            type: "text",
            options: {
                valueAsNumber: true,
            },
            label: " ",
            placeholder: " ...",
            error: errors.postal_code,
        },
        {
            name: "website",
            type: "text",
            label: " ",
            placeholder: "https://mycompany.ru...",
            error: errors.website,
        },
    ]

    return <form onSubmit={handleSubmit(onLocalSubmit)}>
        <h3>{headerText}</h3>
        <AppInputWrapper
            label={" "}
            errors={errors}
            name={"company_type"}>
            <select
                disabled={!editable}
                {...register("company_type", {required: " "})}
            >
                {COMPANY_TYPE_LIST.map(({key, name}) =>
                    <option value={key} key={key.toString()}>
                        {name}
                    </option>)}
            </select>
        </AppInputWrapper>
        {dataArray.map((
            {
                name,
                label,
                options,
                type = "text",
                placeholder
            }) =>
            <AppInputWrapper
                key={name}
                label={label}
                errors={errors}
                name={name}>
                <input disabled={!editable} {...register(name, options,
                    //{ required: "Allo!!" }
                )}
                       placeholder={placeholder} type={type}/>
            </AppInputWrapper>
        )}
        <input type="submit" value={buttonText}/>
    </form>

    // return <FormPattern
    //     headerText={headerText}
    //     buttonText={buttonText}
    //     onClick={onClickLocal}
    //     error={totalError}
    //     editable={editable}
    // >
    //     <div className={styles.input_div}>
    //         <AppSelectedList data={COMPANY_TYPE_LIST}
    //                          error={errors.company_type}
    //                          label={" "}
    //                          active={editable}
    //                          activeIndex={company_type}
    //                          setActiveIndex={setCompanyType}/>
    //     </div>
    //     {dataArray.map((
    //         {
    //             value,
    //             setValue,
    //             label,
    //             placeholder,
    //             error
    //         }, index) =>
    //         <div key={index.toString()} className={styles.input_div}>
    //             <AppInput value={value}
    //                       setValue={setValue}
    //                       label={label}
    //                       editable={editable}
    //                       placeholder={placeholder}
    //                       error={error}
    //             />
    //         </div>)}
    // </FormPattern>
}

export default CompanyInformationForm;
