import {
    SET_HEADER_BACK_INFO, SET_HEADER_BACK_KEY_WORD, SET_HEADER_TITLE_KEY_WORD,
    SET_HEADER_TITLES
} from "../types";

const initialState = {
    title: "",
    subTitle: "",
    backTitle: "",
    backLink: "",
    backKeyWord: "",
    titleKeyWord: "",
};


export const HeaderReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_HEADER_TITLES:
            return {
                ...state,
                ...action.payload,
            };
        case SET_HEADER_BACK_INFO:
            return {
                ...state,
                ...action.payload,
            };
        case SET_HEADER_BACK_KEY_WORD:
            return {
                ...state,
                ...action.payload,
            };
        case SET_HEADER_TITLE_KEY_WORD:
            return {
                ...state,
                ...action.payload,
            };

        default:
            return state;
    }
};
