import React, {useCallback, useEffect, useState} from "react";
import cn from "classnames";
import styles from "./styles.module.scss";
import {ErrorMessage} from "@hookform/error-message";
import {AppWhiteBlock} from "../AppWhiteBlock";

function AppPreviewProduct(
    {
        id,
        onClick = () => {},
        name = "",
        image = "",
        price = 0,
        description = "",
        inverse=false,
    }) {

    const onClickLocal = useCallback(() => {
        onClick(id);
    }, [onClick, id])

    return <AppWhiteBlock
        onClick={onClickLocal}
        className={cn(styles.container, inverse && styles.row_container)}>
        <div className={styles.image_container}>
            {image ?
                <img src={image} alt={"product"}/> :
                <div className={styles.default_image}/>}
        </div>
        <div className={styles.text_container}>
            <h6>{name}</h6>
            <p className={styles.price}>{price ? `${price} ` : "  "}</p>
            <p className={styles.description}>{description}</p>
        </div>
    </AppWhiteBlock>;
}


export default AppPreviewProduct;
