import {
    UPDATE_USER,
    SET_USER_DATA,
    REMOVE_USER,
    OPEN_ENTER_FORM,
    CLOSE_ENTER_FORM,
    ADD_COMPANY,
    SET_COMPANIES,
    ADD_FACTORY
} from "../types";

export const loadUserData = (data) => {
    return async (dispatch) => {
        dispatch({
            type: SET_USER_DATA,
            payload: data,
        });
    };
};

export const updateUser = (data) => {
    return async (dispatch) => {
        dispatch({
            type: UPDATE_USER,
            payload: data,
        });
    };
};

export const waitingForUserData = () => {
    return async (dispatch) => {
        dispatch({
            type: UPDATE_USER,
            payload: {loading: true},
        });
    };
};


export const falseLoadingForUserData = () => {
    return async (dispatch) => {
        dispatch({
            type: UPDATE_USER,
            payload: {loading: false},
        });
    };
};


export const setUserToken = (token) => {
    return async (dispatch) => {
        dispatch({
            type: UPDATE_USER,
            payload: {token},
        });
    };
};


export const setUserCompanyToken = (companyToken) => {
    return async (dispatch) => {
        dispatch({
            type: UPDATE_USER,
            payload: {companyToken},
        });
    };
};


export const addCompany = (company) => {
    return async (dispatch) => {
        dispatch({
            type: ADD_COMPANY,
            payload: company,
        });
    };
};

export const setCompanies = (companies) => {
    return async (dispatch) => {
        dispatch({
            type: SET_COMPANIES,
            payload: companies,
        });
    };
};


export const addFactory = (factory) => {
    return async (dispatch) => {
        dispatch({
            type: ADD_FACTORY,
            payload: factory,
        });
    };
};


export const openEnterForm = () => {
    return async (dispatch) => {
        dispatch({
            type: OPEN_ENTER_FORM,
            payload: {},
        });
    };
};

export const closeEnterForm = () => {
    return async (dispatch) => {
        dispatch({
            type: CLOSE_ENTER_FORM,
            payload: {},
        });
    };
};


export const removeUser = () => ({
    type: REMOVE_USER,
});