import React, {useEffect, useState} from 'react';
import styles from "./styles.module.scss";
import AppButton from "../../../components/ui/AppButton";
import {AppWhiteBlock} from "../../../components/ui/AppWhiteBlock";


function FormPattern(
    {
        error = "",
        buttonText = "",
        headerText = "",
        onClick = () => {},
        editable = true,
        children,
    }
) {

    return <AppWhiteBlock
        className={styles.div_user_data}
    >
        <div className={styles.headers}>
            <div className={styles.head_text}>{headerText}</div>
            <p className={styles.errors}>{error}</p>
        </div>
        <div className={styles.fields}>
            {children}
        </div>
        {editable && <AppButton onClick={onClick}>
            {buttonText}
        </AppButton>}
    </AppWhiteBlock>
}

export default FormPattern;
