import React, {useState} from 'react';
import styles from "./styles.module.scss";
import {falseLoadingForUserData, waitingForUserData} from "../../../../store/actions/user";
import {useDispatch} from "react-redux";
import RequestWrapper from "../../../../helpers/RequestWrapper";
import {COMPANY_URL, MAIN_URL, ORGANIZATIONS_URL} from "../../../../constants";
import AddCompanyUserForm from "../../../../containers/Forms/AddCompanyUserForm";
import RequestView from "../../../../components/RequestView";
import {addNotification} from "../../../../store/actions/content";
import {AppWhiteBlock} from "../../../../components/ui/AppWhiteBlock";
import AppButton from "../../../../components/ui/AppButton";

const GET_REQUEST = "organizations/company/get/"


function CompanyAddUserPage({history, match}) {

    const companyId = match.params.company;

    const onSave = () => {
        history.push(MAIN_URL.ORGANIZATIONS + ORGANIZATIONS_URL.COMPANIES +
            `/${companyId}/staff/`)
    }

    const [users, setUsers] = useState([]);
    const [loading, setLoading] = useState(true);
    const [name, setName] = useState("");

    const onRequest = (res) => {
        setName(res.json.info.name)
        setLoading(false)
    }

    const {REQUEST} = RequestWrapper();
    const dispatch = useDispatch();

    const [errors, setErrors] = useState({})
    const [error, setError] = useState("")

    const onAddStaff = (body) => {
        dispatch(waitingForUserData())
        REQUEST.POST({
            request: "organizations/company/users/add/",
            body: {...body, id: companyId},
        }).then(res => {
            dispatch(falseLoadingForUserData())
            try {
                console.log("|>>>>> NEW STAFF USERS:", res.json)
                if (res.status === 200) {
                    // onSave()
                    setUsers(res.json)
                    dispatch(addNotification({
                        message: " ",
                        code: 0,
                        id: Date.now(),
                        positive: true
                    }))
                } else {
                    setErrors(res.json)
                }
            } catch (e) {
                setError(":" + e.toString())
                console.log("ERR GET COMPANY:", e);
            }
        })
    };

    return <RequestView
        request={`${GET_REQUEST}?id=${companyId}`}
        loading={loading}
        needDoRequest={!name}
        onRequest={onRequest}
    >
        {users.length === 0 ?
            <AddCompanyUserForm
                onSubmit={onAddStaff}
                requestErrors={errors}
                error={error}
                companyName={name}
            /> : <div>
                <h2> </h2>
                <h6>    ,        
                    .   </h6>

                {users.map((item) => <AppWhiteBlock>
                    <div
                        className={styles.user_div}
                        key={item.id.toString()}>
                        <p>{`: ${item.username}`}</p>
                        <p>{`: ${item.password}`}</p>
                    </div>
                </AppWhiteBlock>)}
                <AppButton onClick={onSave}>
                    
                </AppButton>
            </div>}
    </RequestView>
}

export default CompanyAddUserPage;
