import React, {useCallback, useEffect, useLayoutEffect, useMemo, useRef, useState} from 'react';
import styles from "./styles.module.scss";
import {useDispatch, useSelector} from "react-redux";
import bell_svg from '../../../../assets/media/icons/bell.svg';


export function HeaderUser({onClick, path, history, children}) {

    const {first_name, last_name, email} = useSelector(state => state.user)

    const plug = useMemo(() => {
        let l1 = first_name ? first_name.slice(0, 1).toUpperCase() : ""
        let l2 = last_name ? last_name.slice(0, 1).toUpperCase() : ""
        return `${l1}${l2}`
    }, [first_name, last_name])

    return <div className={styles.wrapper}>
        <img className={styles.bell}
             alt={"bell"}
             src={bell_svg}/>
        <div className={styles.image}>
            <p>{plug}</p>
        </div>
        <div className={styles.user_name_container}>
            <h6>{`${first_name} ${last_name}`}</h6>
            <p>{email}</p>
        </div>
    </div>;
}
