import React, {useCallback, useEffect, useState} from 'react';
import styles from "./styles.module.scss";
import AppModal from "../../ui/AppModal";
import {useDispatch, useSelector} from "react-redux";
import {closeEditImage, openEditImage} from "../../../store/actions/content";
import AppButton from "../../ui/AppButton";
import RequestWrapper from "../../../helpers/RequestWrapper";
import {falseLoadingForUserData, waitingForUserData} from "../../../store/actions/user";

const EDIT_IMAGE = "products/image/update/"

function EditImageModal() {

    const [modalVisible, setModalVisible] = useState(false)
    const [description, setDescription] = useState("")
    const dispatch = useDispatch();
    const image = useSelector(state => state.content.image)
    const {REQUEST} = RequestWrapper();
    const [descriptionError, setDescriptionError] = useState("")

    const onCloseImage = useCallback(() => {
        document.body.classList.remove("noscroll")
        setDescriptionError("")
        dispatch(closeEditImage())
    }, [])

    const onSave = () => {
        dispatch(waitingForUserData())
        REQUEST.POST({
            request: EDIT_IMAGE,
            body: {
                image: image.id,
                description,
            },
            onSuccess: {message: " ."}
        }).then(res => {
            dispatch(falseLoadingForUserData())
            if (res.status === 200) {
                // onCloseImage()
                dispatch(openEditImage({...image, description}))
            } else {
                if (res.json.description) {
                    setDescriptionError(res.json.description)
                }
            }
        })
    }

    useEffect(() => {
        if (image.link) {
            document.body.classList.add("noscroll")
            setDescription(image.description)
            setModalVisible(true)
        }
    }, [image])

    return <AppModal
        modalVisible={modalVisible}
        setModalVisible={setModalVisible}
        onClose={onCloseImage}
        showLoading={false}
        showWhiteBlock={true}
        canCloseOnBackdrop={false}>
        <h5> </h5>
        <div className={styles.container}>
            <div className={styles.image_container}>
                <img
                    alt={"img"}
                    src={image.link}
                    // className={styles.img}
                />
            </div>
            <div className={styles.edit_container}>
                <div className={styles.description_container}>
                    <label>
                         ( 300 )
                    </label>
                    <textarea
                        value={description}
                        onChange={(e) => setDescription(e.target.value)}
                    />
                    <p className={styles.error}>
                        {descriptionError}
                    </p>
                </div>
                <AppButton inverse onClick={onSave}>
                    
                </AppButton>
            </div>

        </div>
    </AppModal>;
}


export default EditImageModal;
