import React, {useEffect, useState} from 'react';
import styles from "./styles.module.scss";
import RequestWrapper from "../../../helpers/RequestWrapper";
import {useDispatch, useSelector} from "react-redux";
import {falseLoadingForUserData, loadUserData, waitingForUserData} from "../../../store/actions/user";
import RequestView from "../../../components/RequestView";
import AppButton from "../../../components/ui/AppButton";
import {ORGANIZATIONS_URL, COMPANY_URL, MAIN_URL, COMPANY_ROLE_LIST} from "../../../constants";
import {AppWhiteBlock} from "../../../components/ui/AppWhiteBlock";


const COMPANY_LIST_REQUEST = "organizations/company/list/"

const GetRole = (index) => {
    try {
        return COMPANY_ROLE_LIST.find(i => i.key === index).name
    } catch (e) {
        return ""
    }
}

function CompaniesPage({history}) {

    const onAddCompany = () => {
        window.scroll(0, 0);
        history.push(MAIN_URL.ORGANIZATIONS +
            ORGANIZATIONS_URL.COMPANIES + COMPANY_URL.ADD);
    }

    const onCompany = (id) => {
        window.scroll(0, 0);
        history.push(MAIN_URL.ORGANIZATIONS +
            ORGANIZATIONS_URL.COMPANIES + `/${id}` + COMPANY_URL.INFO);
    }

    const {REQUEST} = RequestWrapper()
    const dispatch = useDispatch();
    const [list, setList] = useState([])
    const [loading, setLoading] = useState(true)

    const onRequest = (res) => {
        console.log("|>>>>> COMPANIES:", res.json)
        // dispatch(falseLoadingForUserData())
        setLoading(false)
        if (res.status === 200) {
            setList(res.json.company_list)
        } else {

        }
    }

    const GetCompanies = (res) => {
        dispatch(waitingForUserData())
        REQUEST.GET({
            request: "organizations/company/list/",
        }).then(onRequest)
    };

    return <RequestView
        loading={loading}
        request={COMPANY_LIST_REQUEST}
        onRequest={onRequest}
    >
        {list.length > 0 ? <div className={styles.container}>
            {list.map((item, index) =>
                <AppWhiteBlock
                    key={index.toString()}
                    onClick={() => onCompany(item.id)}
                    className={styles.company}>
                    <h5>{item.name}</h5>
                    <p>{GetRole(item.role)}</p>
                </AppWhiteBlock>)}
            <AppButton onClick={onAddCompany}>
                 
            </AppButton>
        </div> : <div className={styles.no_companies_div}>
            <p>    </p>
            <AppButton onClick={onAddCompany}>
                 
            </AppButton>
        </div>}
    </RequestView>
}

export default CompaniesPage;
