import React, {useEffect, useLayoutEffect} from "react";
import {Redirect, Route, Switch} from 'react-router-dom';
import {
    PROFILE_URL,
    MAIN_URL,
    ORGANIZATIONS_URL,
    COMPANY_URL,
    PRODUCT_URL,
    ORDERS_URL,
    FACTORY_URL
} from "../constants";
import {useDispatch, useSelector} from "react-redux";
import {setHeaderBackInfo, setHeaderSubTitle, setHeaderTitle} from "../store/actions/header";
import {addCompany, addFactory} from "../store/actions/user";
import RequestWrapper from "../helpers/RequestWrapper";
import AddFactoryPage from "../pages/CompaniesPages/FactoriesPages/AddFactoryPage";
import CompanyReceivedRequestPage from "../pages/CompaniesPages/CompanyPage/CompanyReceivedRequestPage";
import FactoryInformationPage from "../pages/CompaniesPages/FactoriesPages/FactoryInformationPage";
import FactoryProductsPage from "../pages/CompaniesPages/FactoriesPages/FactoryProductsPage";
import CompanyAddProductPage from "../pages/CompaniesPages/ProductsPage/CompanyAddProductPage";
import FactoryAddProductPage from "../pages/CompaniesPages/ProductsPage/FactoryAddProductPage";
import EditProductPage from "../pages/CompaniesPages/ProductsPage/EditProductPage";
import CompanyProductFiles from "../pages/CompaniesPages/ProductsPage/CompanyProductFiles";
import FactoryProductFiles from "../pages/CompaniesPages/ProductsPage/FactoryProductFiles";
import FactoryStaffPage from "../pages/CompaniesPages/FactoriesPages/FactoryStaffPage";
import CompanyAddUserPage from "../pages/CompaniesPages/CompanyPage/CompanyAddUserPage";
import FactoryAddUserPage from "../pages/CompaniesPages/FactoriesPages/FactoryAddUserPage";
import FactoryReceivedRequestsPage from "../pages/CompaniesPages/FactoriesPages/FactoryReceivedRequestsPage";


const GET_REQUEST = "organizations/factory/get/"
// const PERMISSIONS_REQUEST = "organizations/company/permissions/"


const FactoryRouter = ({match}) => {

    // console.log("|> Company router match:", match)

    const companyId = match.params.company;
    const factoryId = match.params.factory;
    const dispatch = useDispatch()
    const {REQUEST} = RequestWrapper()

    const backKeyWord = useSelector(state => state.header.backKeyWord)
    const titleKeyWord = useSelector(state => state.header.titleKeyWord)

    const START = MAIN_URL.ORGANIZATIONS + ORGANIZATIONS_URL.COMPANIES + "/:company" +
        COMPANY_URL.FACTORIES + "/:factory";

    useLayoutEffect(() => {
        if (backKeyWord === "company_from_factory") {
            dispatch(setHeaderBackInfo({
                backTitle: " ",
                backLink: `/organizations/companies/${companyId}`,
            }))
        }
    }, [backKeyWord])

    const cacheFactories = useSelector(state => state.user.factories)

    useEffect(() => {
        if (titleKeyWord === "factory") {
            if (cacheFactories.findIndex(i => (i.id === factoryId && i.name)) < 0) {
                REQUEST.GET({request: `${GET_REQUEST}?id=${factoryId}`}).then(res => {
                    if (res.status === 200) {
                        dispatch(setHeaderTitle(res.json.info.name));
                        dispatch(setHeaderSubTitle(""));
                        dispatch(addFactory(res.json.info));
                    }
                })
            } else {
                let name = cacheFactories.find(i => (i.id === factoryId && i.name)).name
                dispatch(setHeaderTitle(name));
                dispatch(setHeaderSubTitle(""));
            }
        }
    }, [factoryId, titleKeyWord])


    return <Switch>
        <Route exact path={START + FACTORY_URL.INFO}
               component={FactoryInformationPage}/>
        <Route exact path={START + FACTORY_URL.ADD_PRODUCT}
               component={FactoryAddProductPage}/>
        <Route exact path={START + FACTORY_URL.PRODUCTS_FILES}
               component={FactoryProductFiles}/>
        <Route exact path={START + FACTORY_URL.PRODUCTS + "/:product"}
               component={EditProductPage}/>
        <Route exact path={START + FACTORY_URL.PRODUCTS}
               component={FactoryProductsPage}/>
        <Route exact path={START + FACTORY_URL.STAFF}
               component={FactoryStaffPage}/>
        <Route exact path={START + FACTORY_URL.ADD_STAFF}
               component={FactoryAddUserPage}/>
        <Route exact path={START + FACTORY_URL.ORDERS}
               component={FactoryReceivedRequestsPage}/>

        {/*<Route exact path={START + ORDERS_URL.CREATED_REQUESTS}*/}
        {/*       component={CompanyReceivedRequestPage}/>*/}
        <Redirect to={
            MAIN_URL.ORGANIZATIONS + ORGANIZATIONS_URL.COMPANIES + `/${companyId}`
            + COMPANY_URL.FACTORIES + `/${factoryId}` + FACTORY_URL.INFO
        }/>
    </Switch>
};


export default FactoryRouter;
