import {DB_URL} from "../constants";

const TimeoutPromise = ({ms = 10000, promise}) => {
    const totalMs = ms || 10000;
    return new Promise((resolve, reject) => {
        const timeoutId = setTimeout(() => {
            // {status: 408, json: {}, message: "   "}
            reject(new Error("   "))
        }, totalMs);
        promise.then(
            (res) => {
                clearTimeout(timeoutId);
                resolve(res);
            },
            (err) => {
                clearTimeout(timeoutId);
                reject(err);
            }
        );
    })
}

export const httpGet = async ({request, token = "", ms = 10000}) => {
    try {
        let headers = {
            "Content-Type": "application/json"
        };
        if (token) {
            headers = {
                'Authorization': 'Token ' + token,
                "Content-Type": "application/json"
            }
        }
        const response = await TimeoutPromise({
            ms, promise: fetch(`${DB_URL.URL}/${request}`, {
                method: 'GET',
                headers: headers
            })
        });
        if (response.ok) {
            //console.log("GET OK", response);
            const json = await response.json();
            return {json: json, message: "", status: 200, type: 0}
        } else {
            let message = "";
            if (response.status === 400) {
                message = " :   "
            } else if (response.status === 401) {
                message = "  "
            } else if (response.status === 404) {
                message = "  "
            } else if (response.status === 409) {
                message = "     "
            } else if (response.status === 403) {
                message = " "
            } else {
                message = ",  " + response.status.toString(10)
            }
            let json = {}
            try {
                json = await response.json();
            } catch (e) {
                json = {}
            }
            //const json = await response.json();
            return {json: json, message: message, status: response.status, type: 1}
        }
    } catch (err) {
        console.warn('httpGet error ', err);
        return {json: {}, message: err.toString(), status: 0, type: 2}
    }
};

export const httpPost = async (
    {
        request,
        fileType = false,
        token = "", body, ms = 10000
    }) => {
    try {
        let headers = {
            "Content-Type": "application/json"
        };
        if (fileType) {
            headers = {};
        }
        if (token.length > 0) {
            headers['Authorization'] = 'Token ' + token
            // headers = {
            //     'Authorization': 'Token ' + token,
            //     "Content-Type": "application/json"
            // }
        }
        let fetchBody = {
            method: 'POST',
            headers: headers,
        }
        if (fileType) {
            fetchBody['body'] = body;
        } else {
            fetchBody['body'] = JSON.stringify(body);
        }
        const response = await TimeoutPromise({
            ms, promise: fetch(`${DB_URL.URL}/${request}`,
                fetchBody)
        });
        if (response.ok) {
            //console.log("POST OK", response);
            const json = await response.json();
            return {json: json, message: "", status: 200, type: 0}
        } else {
            let message = "";
            //console.log("RRR", response);
            if (response.status === 400) {
                message = "  ,   ..."
            } else if (response.status === 401) {
                message = "  "
            } else if (response.status === 404) {
                message = "  ,      :("
            } else if (response.status === 409) {
                message = "     !  :)"
            } else if (response.status === 403) {
                message = " "
            } else {
                message = ",  " + response.status.toString(10)
            }
            let json = {}
            try {
                json = await response.json();
            } catch (e) {
                json = {}
            }
            return {json: json, message: message, status: response.status, type: 1}
        }
    } catch (err) {
        console.warn('httpPost error ', err);
        return {json: {}, message: err.toString(), status: 0, type: 2}
    }
};