import React, {useCallback, useEffect, useRef, useState} from 'react';
import styles from "./styles.module.scss";
import RequestView from "../../../../../components/RequestView";
import {AppWhiteBlock} from "../../../../../components/ui/AppWhiteBlock";
import RequestWrapper from "../../../../../helpers/RequestWrapper";
import AppButton from "../../../../../components/ui/AppButton";

const GET_REQUEST_LIST = "organizations/company/partners/list/"
const GET_REQUEST_REQUESTS = "organizations/company/partners/request/list/"
const SEARCH_REQUEST = "organizations/company/search/"
const ADD_PARTNER = "organizations/company/partners/add/"


const CompanyPartner = ({name = "", inn = "-"}) => {
    return <AppWhiteBlock className={styles.partner_container}>
        <h6>{name}</h6>
        <p>{`: ${inn}`}</p>
    </AppWhiteBlock>
}

const RequestPartner = ({
                            name, confirm, inn = "-", id, onSubmit = () => {
    }
                        }) => {
    // const [status, setStatus] = useState(undefined)
    // const [loading, setLoading] = useState(false)

    const onConfirm = useCallback(() => onSubmit({id, confirm: true}), [id, onSubmit])
    const onReject = useCallback(() => onSubmit({id, confirm: false}), [id, onSubmit])

    return <AppWhiteBlock className={styles.partner_container}>
        <h6>{name}</h6>
        <p>{`: ${inn}`}</p>
        {typeof confirm === 'undefined' ?
            <div className={styles.buttons_request}>
                <AppButton type={"green"} onClick={onConfirm}>
                    
                </AppButton>
                <AppButton type={"red"} onClick={onReject}>
                    
                </AppButton>
            </div> : confirm ? <h6 className={styles.confirm}>
                
            </h6> : <h6 className={styles.reject}>
                
            </h6>}
    </AppWhiteBlock>
}


const PartnerSearch = ({companyId}) => {
    const [q, setQ] = useState("")
    const [id, setId] = useState(0)
    const [list, setList] = useState([])
    const timeout = useRef(undefined)
    const {REQUEST} = RequestWrapper();

    useEffect(() => {
        clearTimeout(timeout.current)
        console.log("Q:", q)
        timeout.current = setTimeout(() => {
            console.log("START REQ:", q)
            REQUEST.GET({request: `${SEARCH_REQUEST}?q=${q}`}).then(res => {
                if (res.status !== 200) {
                    setList([])
                } else {
                    setList(res.json.list.map((item, index) => {
                        item.value = `${index + 1}. ${item.name}`
                        item.label = `: ${item.inn}`
                        return item
                    }))
                }
            })
        }, 700)

    }, [q])

    const [loading, setLoading] = useState(false)

    const onSubmit = useCallback(() => {
        if (!id) {
            return
        }
        setLoading(true)
        REQUEST.POST({
            request: ADD_PARTNER,
            body: {
                company_id: companyId,
                partner_id: id,
            },
            onSuccess: {message: " "}
        }).then(res => {
            setLoading(false)
            if (res.status === 200) {
                setQ("")
            }
        })
    }, [companyId, id])

    useEffect(() => {
        let ind = list.findIndex(i => i.value === q)
        if (ind >= 0) {
            clearTimeout(timeout.current)
            setId(list[ind].id)
        } else {
            setId(0)
        }
    }, [q, list])

    useEffect(() => console.log("ID P:", id), [id])

    // useEffect(() => {
    //     let getValue = document.getElementsByName("search")[0]
    //     getValue.addEventListener("input", () => {
    //         // document.getElementById("sp").innerHTML="SELECT:" + this.value;
    //         let opt = $('option[value="'+this.val()+'"]');
    //         alert(opt.length ? opt.attr('id') : 'NO OPTION');
    //     })
    // }, [])

    return <AppWhiteBlock className={styles.search_container}>
        <h5></h5>
        <div className={styles.search_line}>
            <input
                //className={styles.search_input}
                type={"text"}
                list={"search_list"}
                name={"search"}
                placeholder={"  ..."}
                value={q} onChange={e => {
                // console.log(e.target)
                setQ(e.target.value)
            }}/>
            <datalist id="search_list">
                {list.map(item => <option
                    className={styles.search_option}
                    key={item.id.toString()} {...item}/>)}
                {/*<option id="op1" value="VVO" label=": 794970567450"/>*/}
                {/*<option id="op2" value="MEM" label=""/>*/}
                {/*<option //value="VKO____"*/}
                {/*        name={"VNUKO"}*/}
                {/*        data-id={5}*/}
                {/*        id="op3"*/}
                {/*        // onChange={() => setId(5)}*/}
                {/*        // onBlur={() => setId(5)}*/}
                {/*        // onClick={() => setId(5)}*/}
                {/*        label=" ">VKO</option>*/}
                {/*<option value="LNX" label=""/>*/}
                {/*<option value="YKS" label=""/>*/}
            </datalist>
            <AppButton type={id ? "" : "gray"} onClick={onSubmit}>
                {loading ? "..." : " "}
            </AppButton>
        </div>
    </AppWhiteBlock>

}


function CompanyPartnersSection(
    {
        companyId = 0,
        setPartnersRequestAmount = () => {
        },
        editable = true,
    }) {

    const [loading, setLoading] = useState(false)
    const [firstRequest, setFirstRequest] = useState(false)
    const [requests, setRequests] = useState([])
    const [partners, setPartners] = useState([])
    const {REQUEST} = RequestWrapper();

    useEffect(() => {
        // console.log("EDITABLE:", editable)
        if (editable && companyId) {
            REQUEST.GET({request: `${GET_REQUEST_REQUESTS}?id=${companyId}`}).then(res => {
                console.log("REQUESTS FOR PARTNERS:", res)
                if (res.status === 200) {
                    setRequests(res.json.list)
                }
            })
        }
    }, [editable, companyId])

    const onAnswerRequest = useCallback(({id, confirm}) => {
        // console.log("REQ BODY:", companyId, id)
        const req = "organizations/company/partners/" + (confirm ? "confirm/" : "reject/")
        setLoading(true)
        REQUEST.POST({
            request: req,
            body: {
                company_id: companyId,
                partner_id: id
            }
        }).then(res => {
            setLoading(false);
            console.log("RES ANSWER:", res)
            if (res.status === 200) {
                try {
                    setPartnersRequestAmount(r => r - 1)
                    let newReq = requests.map(i => {
                        let item = {...i}
                        if (item.id === id) {
                            item.confirm = confirm
                        }
                        return item
                    })
                    console.log("NEW REQ:", newReq)
                    setRequests(newReq)
                } catch (e) {
                    //
                }
            }
        })
    }, [requests, companyId])

    const onRequest = (res) => {
        console.log("PARTNERS LIST:", res)
        setPartners(res.json.list)
        if (res.status === 200) {
            setFirstRequest(true)
        }
    }

    return <RequestView
        needDoRequest={!firstRequest}
        request={`${GET_REQUEST_LIST}?id=${companyId}`}
        loading={loading}
        onRequest={onRequest}
    >
        {/*<h2> </h2>*/}
        {editable && requests.length > 0 && <div className={styles.requests_container}>
            <h4>  </h4>
            <h6>    </h6>
            {requests.map(item => <RequestPartner
                key={item.id.toString()}
                onSubmit={onAnswerRequest}
                // confirmed={false}
                {...item}/>)}
        </div>}

        <PartnerSearch companyId={companyId}/>

        {partners.length > 0 ?
            <div>
                {partners.map(item => <CompanyPartner key={item.id.toString()} {...item}/>)}
            </div> :
            <div>
                <h4>
                       
                </h4>
            </div>
        }
    </RequestView>
}

export default CompanyPartnersSection;
