import React, {useEffect, useState} from 'react';
import styles from "./styles.module.scss";
import {COMPANY_URL, MAIN_URL, ORGANIZATIONS_URL, PRODUCT_URL} from "../../../../constants";
import RequestView from "../../../../components/RequestView";
import {falseLoadingForUserData, waitingForUserData} from "../../../../store/actions/user";
import {useDispatch} from "react-redux";
import RequestWrapper from "../../../../helpers/RequestWrapper";

const GET_REQUEST = "products/company/products/files/"
const ADD_FILE = "products/product/create/file/"


const GetFileStatus = (id) => {
    switch (id) {
        case 2:
            return ""
        case 3:
            return ""
        default:
            return " "
    }
}

function CompanyProductFiles(
    {
        history,
        match,
        editable = true,
    }) {

    const companyId = match.params.company;

    const [q, setQ] = useState("")

    const [loading, setLoading] = useState(false)
    const [files, setFiles] = useState([])
    const [modalVisible, setModalVisible] = useState(false)
    const onRequest = (res) => {
        // console.log("PRODUCTS::::", res.json)
        setFiles(res.json.list)
        setLoading(false)
    }

    const onAddProductFile = () => {
        history.push(MAIN_URL.ORGANIZATIONS + ORGANIZATIONS_URL.COMPANIES +
            `/${companyId}` + COMPANY_URL.ADD_PRODUCT)
    }

    const [fileError, setFileError] = useState("")
    const dispatch = useDispatch();
    const {REQUEST} = RequestWrapper();

    const onUploadFile = (e) => {
        setFileError("")
        let targetFiles = e.target.files;
        if (targetFiles.length >= 0) {
            let file = targetFiles[0];
            // console.log("SET FILE:", file)
            if (true) {
                if (file.size > 50 * 1024 * 1024) {
                    setFileError("     50 MB")
                } else {
                    dispatch(waitingForUserData())
                    let data = new FormData()
                    data.append("file", file)
                    data.append("company", companyId)
                    REQUEST.POST({
                        request: `${ADD_FILE}`, //`${ADD_IMAGE}${productId}`,
                        body: data,
                        fileType: true,
                    }).then(res => {
                        console.log("FILE UPLOAD RES:", res)
                        dispatch(falseLoadingForUserData())
                        if (res.status === 200) {
                            setFiles(f => [...f, res.json])
                        } else {
                            setFileError(res.json.detail)
                        }
                        // console.log("IMAGE UPLOAD RES:", res)
                    })
                }
            } else {
                setFileError("  ")
            }
        }
        document.getElementById("file_upload").value = "";
    }

    return <RequestView
        request={`${GET_REQUEST}?company=${companyId}`}
        loading={loading}
        onRequest={onRequest}
    >
        {/*<AppModal*/}
        {/*    modalVisible={modalVisible}*/}
        {/*    setModalVisible={setModalVisible}*/}
        {/*    showWhiteBlock={true}*/}
        {/*>*/}
        {/*    */}
        {/*</AppModal>*/}
        <div className={styles.header_line}>
            <h6></h6>
            {/*<input type={'search'}*/}
            {/*       placeholder={"..."}*/}
            {/*       value={q} onChange={(e) =>*/}
            {/*    setQ(e.target.value)}*/}
            {/*/>*/}
            <div className={styles.buttons}>
                <div className={styles.upload_container}>
                    <label className={styles.upload_label}
                           htmlFor={"file_upload"}>
                         
                    </label>
                    <input id={"file_upload"}
                           name={"file_upload"}
                           //accept=".doc,.docx,.txt,.pdf,.application/pdf,.xml,application/msword,application/vnd.openxmlformats-officedocument.wordprocessingml.document"
                           onChange={onUploadFile}
                           type={"file"} //value={" "}
                    />
                    <p className={styles.error}>
                        {fileError}
                    </p>
                </div>
            </div>
        </div>
        <p className={styles.help}>
                     .
              ,          .      5     .
                  50 MB.
        </p>
        <table>
            <thead>
            <tr>
                <th></th>
                <th></th>
                {/*<th> </th>*/}
                <th></th>
                <th></th>
            </tr>
            </thead>
            <tbody>
            {files
                .map(({id, name, link, status, datetime, comment}, index) =>
                    <tr key={id.toString()}>
                        <td>
                            <h6>
                                {index + 1}
                            </h6>
                        </td>
                        <td className={styles.name}>
                            <a href={link}>
                                {name}
                            </a>
                        </td>
                        <td className={styles.price}>
                            <p>
                                {GetFileStatus(status)}
                            </p>
                        </td>
                        {/*<td className={styles.description}>*/}
                        {/*    {description}*/}
                        {/*</td>*/}
                        <td>
                            <p>{comment ? comment : "-"}</p>
                        </td>
                    </tr>
                )}
            </tbody>
        </table>
    </RequestView>
}

export default CompanyProductFiles;
