import React from "react";
import st from '../styles/Footer.module.scss'
import MaskedInput from 'react-text-mask'
import {ButtonRegistration} from './button'
import cn from 'classnames'


function Footer({afterEnter}) {
    return(
        <footer>
            <div className={st.social}>
                <div className={st.left}>
                    <div className={st.title}>
                        <span className={st.boldtitle}>,</span><br />  
                    </div>
                    <div className={st.reg}>
                        {/* <div className={st.phone}></div>
                        <MaskedInput
                            mask={[/[8]/, '(', /[9]/, /\d/, /\d/, ')', ' ', /\d/, /\d/, /\d/, '-', /\d/, /\d/, /\d/, /\d/]}
                            className={st.in}
                            placeholder=""
                            showMask={true}
                        /> */}
                        <ButtonRegistration afterEnter={afterEnter}/>
                        {/* <button className={st.bt}></button> */}
                    </div>
                </div>
                {/* <div className={st.center}>
                    <div className={st.description}>  </div>
                    <div className={st.site}>
                        <ul className={st.icons}>
                            <li className={cn(st.vk, st.icon)}></li>
                            <li className={cn(st.google, st.icon)}></li>
                            <li className={cn(st.ya, st.icon)}></li>
                        </ul>
                    </div>
                </div> */}
                <div className={st.right}>
                    <div className={st.picture}></div>
                </div>
            </div>
            <div className={st.copi}>
                <div> Supply Director, 2022</div>
                <div> </div>
            </div>
        </footer>
    );
};

export default Footer