import React, {useEffect, useState} from 'react';
import RequestWrapper from "../../../helpers/RequestWrapper";
import AppModal from "../../../components/ui/AppModal";
import cn from "classnames";
import styles from "./styles.module.scss";
import {AppWhiteBlock} from "../../../components/ui/AppWhiteBlock";
import AppInput from "../../../components/ui/AppInput";
import AppButton from "../../../components/ui/AppButton";
import {useDispatch, useSelector} from "react-redux";
import {loadUserData, waitingForUserData} from "../../../store/actions/user";
import ProfileForm from "../../../containers/Forms/ProfileForm";


function ProfilePage() {

    const {REQUEST} = RequestWrapper()
    const dispatch = useDispatch();
    const user = useSelector(state => state.user)

    // const [loading, setLoading] = useState(true);
    
    const onExit = () => {
        REQUEST.CREATED.EXIT()
    }

    const ErrorPage = () => (
        <div>
            .  .
        </div>
    )

    // const ShowPage = () => {
    //     setTimeout(() => setLoading(false), 3000);
    // }

    // const [page, setPage] = useState(ErrorPage)

    const [userInfoErrors, setUserInfoErrors] = useState({})
    // const [userInfoLoading, set]

    const onSaveUserInfo = (body) => {
        dispatch(waitingForUserData())
        REQUEST.POST({
            request: "users/user/",
            body,
            onSuccess: {message: " "}
        }).then(res => {
            try {
                console.log("|>>>>> NEW Profile user info", res.json)
                if (res.status === 200) {
                    dispatch(loadUserData(res.json))
                } else {
                    dispatch(loadUserData({}))
                    setUserInfoErrors(res.json)
                }
                // if (res.json.is_staff) {
                //     setPage((props) => AdminProfilePage({...props, userData: res.json}))
                // } else {
                    //setPage(StudentProfilePage)
                // }
            } catch (e) {
                console.log("ERR GET USER:", e);
            }
        })
    };


    return <div className={styles.screen}>
        {/*<div className={styles.div_row}>*/}
            <ProfileForm
                data={user}
                onSubmit={onSaveUserInfo}
                requestErrors={userInfoErrors}
            />
        <div className={styles.inner_div_settings}>
            <AppButton onClick={onExit} type="red">
                  
            </AppButton>
        </div>
        {/*</div>*/}
        {/*<div className={styles.div_row_settings}>*/}
        {/*    <AppWhiteBlock className={styles.div_settings}>*/}
        {/*        <div className={styles.inner_div_settings}>*/}
        {/*            <AppButton onClick={onExit} type="red">*/}
        {/*                  */}
        {/*            </AppButton>*/}
        {/*        </div>*/}
        {/*    </AppWhiteBlock>*/}
        {/*</div>*/}
    </div>
}

export default ProfilePage;
