import React, {useEffect, useState} from 'react';
// import styles from "./styles.module.scss";
// import "./styles.module.scss";
import {withRouter} from "react-router-dom";
import {MAIN_URL} from "../../constants";

import Header from "../../components/Landing2/header";
import Main from "../../components/Landing2/main";
import '../../components/styles/style.scss'


function LandingPage({history}) {

    const afterEnter = () => {
        window.scroll(0, 0);
        history.push(MAIN_URL.ORGANIZATIONS)
    }

    useEffect(() => {
        document.title = "SupplyDirector |   ";
    }, [])

    return (
        <div className="App">
          <Header afterEnter={afterEnter} history={history}/>
          <Main afterEnter={afterEnter} history={history}/>
        </div>
      );
    
}

export default withRouter(LandingPage);
