import React, {useEffect, useState} from 'react';
import styles from "./styles.module.scss";
import RequestView from "../../../../../components/RequestView";
import {AppWhiteBlock} from "../../../../../components/ui/AppWhiteBlock";
import {COMPANY_URL, FACTORY_URL, MAIN_URL, ORGANIZATIONS_URL} from "../../../../../constants";
import AppButton from "../../../../../components/ui/AppButton";

const GET_REQUEST = "organizations/company/factories/list/"

const Factory = ({id, name, onClick}) => {
    return <AppWhiteBlock
        onClick={() => onClick(id)}
        className={styles.factory}>
        <h6>{name}</h6>
    </AppWhiteBlock>
}

function CompanyFactoriesSection(
    {
        companyId,
        history,
        editable=true,
    }) {

    const [loading, setLoading] = useState(false)
    const [list, setList] = useState([])
    const onRequest = (res) => {
        console.log("FACTORIES:", res)
        if (res.json.list)
        setList(res.json.list)
    }

    const onAddFactory = () => {
        history.push(
            MAIN_URL.ORGANIZATIONS + ORGANIZATIONS_URL.COMPANIES + `/${companyId}` +
            COMPANY_URL.ADD_FACTORY
        )
    }

    const onClickFactory = (id) => {
        history.push(
            MAIN_URL.ORGANIZATIONS + ORGANIZATIONS_URL.COMPANIES + `/${companyId}` +
            COMPANY_URL.FACTORIES + `/${id}` + FACTORY_URL.INFO
        )
    }

    return <RequestView
        // needDoRequest={false}
        request={`${GET_REQUEST}?id=${companyId}`}
        loading={loading}
        onRequest={onRequest}
    >
        {list.map((item) => <Factory
            onClick={onClickFactory}
            key={item.id.toString()} {...item}/>)
        }
        <AppButton onClick={onAddFactory}>
             
        </AppButton>
    </RequestView>
}

export default CompanyFactoriesSection;
