import React, {useEffect, useLayoutEffect, useRef} from 'react';
import {useCookies} from 'react-cookie';
import {COOKIES_NAMES, ROLES, STATUS_USER_COOKIES} from "../constants";
import {httpGet, httpPost} from "./networks";
import {useDispatch, useSelector} from "react-redux";
import {addNotification} from "../store/actions/content";
import {setUserToken, setUserCompanyToken} from "../store/actions/user";


const GetCorrectRequest = (request) => {
    if (typeof request === 'string') {
        return request
    } else {
        return request.N
    }
}

const RequestWrapper = (children) => {
    const [cookie, setCookie, removeCookie] = useCookies(
        [COOKIES_NAMES.TOKEN, COOKIES_NAMES.COMPANY_TOKEN, COOKIES_NAMES.ROLE, COOKIES_NAMES.STATUS]);

    useLayoutEffect(() => {
        // currentToken.current = cookie.token;
        dispatch(setUserToken(cookie.token))
        dispatch(setUserCompanyToken(cookie.companyToken))
    }, [cookie])
    const storeToken = useSelector(state => state.user.token)
    const companyToken = useSelector(state => state.user.companyToken)
    const dispatch = useDispatch();

    const AddNewError = (error) => {
        dispatch(addNotification({positive: false, ...error, id: Date.now()}))
    }

    const SetErrorToken = () => {
        dispatch(setUserToken(""))
        dispatch(setUserCompanyToken(""))
        removeCookie(COOKIES_NAMES.TOKEN, {path: '/'});
        removeCookie(COOKIES_NAMES.ROLE, {path: '/'});
        removeCookie(COOKIES_NAMES.STATUS, {path: '/'})
    }

    const SetRole = ({result}) => {
        if (result.json.is_teacher) {
            setCookie(COOKIES_NAMES.ROLE, ROLES.TEACHER, {path: '/'})
            setCookie(COOKIES_NAMES.STATUS, STATUS_USER_COOKIES.USER, {path: '/'})
        } else if (result.json.is_staff) {
            setCookie(COOKIES_NAMES.STATUS, STATUS_USER_COOKIES.ADMIN, {path: '/'})
            if (!cookie.role) {
                setCookie(COOKIES_NAMES.ROLE, ROLES.ADMIN, {path: '/'})
            }
        } else {
            setCookie(COOKIES_NAMES.ROLE, ROLES.STUDENT, {path: '/'})
            setCookie(COOKIES_NAMES.STATUS, STATUS_USER_COOKIES.USER, {path: '/'})
        }
    }

    const SetAdminRole = (role) => {
        if (cookie.status === STATUS_USER_COOKIES.ADMIN) {
            setCookie(COOKIES_NAMES.ROLE, role, {path: '/'})
        }
    }

    // const SetRoleRequest = ({token = cookie.token}) => {
    //     GETWithToken({request: "users/user/", token}).then(result => SetRole({result}))
    // }

    const CheckerRequest = ({result, meta, onSuccess}) => {
        try {
            if (result.status === 401) {
                // SetErrorToken()
                AddNewError({code: 401, message: " "});
                return false
            } else if (result.status === 403) {
                if (meta.strong_access) {
                    // SetRoleRequest({});
                }
                AddNewError({code: 403, message: " "})
                return false
            } else if (result.status === 404) {
                let mes = " "
                try {
                    if (result.json.detail) {
                        mes = result.json.detail
                    }
                } catch (e) {
                    //
                }
                AddNewError({code: 404, message: mes})
                return false
            } else if (result.status !== 200) {
                try {
                    if (result.json.detail) {
                        AddNewError({code: result.status, message: result.json.detail})
                        // mes = result.json.detail
                    }
                } catch (e) {
                    //
                }
                // AddNewError({code: result.status || 0, message: result.message})
                return false
            }
            if (onSuccess.message) {
                AddNewError({code: 200, positive: true, message: onSuccess.message})
            }
            //AddNewError({code: 200, message: " !"})
            return true
        } catch (e) {
            console.log("Checker error", e);
            return true;
        }
    }

    const InnerHandler = ({result, handler, meta, onSuccess}) => {
        try {
            const isCorrect = CheckerRequest({result, meta, onSuccess})
            if (!isCorrect) {
                console.log("Checker catch errors:", result.message)
            } else {
                if (meta.login) {
                    const token = result.json["Token"];
                    //SetRoleRequest({token});
                    setCookie(COOKIES_NAMES.TOKEN, token, {path: '/'})
                    // currentToken.current = token;
                    dispatch(setUserToken(token));
                    // SetRoleRequest({token});
                }
                if (meta.companyLogin) {
                    const token = result.json["token"];
                    setCookie(COOKIES_NAMES.COMPANY_TOKEN, token, {path: '/'})
                    dispatch(setUserCompanyToken(token));
                }
                // if (meta.first) {
                //     SetRole({result});
                // }
            }
        } catch (e) {

        }
        if (handler) {
            return handler(result)
        } else {
            return Promise.resolve(result)
        }
    }

    const onExit = () => {
        SetErrorToken()
    }

    const GETWithToken = ({request, handler, token, meta = {}}) => {
        return httpGet({request: GetCorrectRequest(request), token, ms: meta.ms})
            .then(result => InnerHandler({result, handler, meta}))
    }

    const GET = ({request, handler, onSuccess = {message: ""}, 
                    fromCompany = false, meta = {}}) => {
        let totalToken = fromCompany ? (companyToken ? companyToken : cookie.companyToken) :
            storeToken ? storeToken : cookie.token;
        return httpGet({
            request: GetCorrectRequest(request),
            token: totalToken, 
            ms: meta.ms
        })
            .then(result => InnerHandler({result, handler, meta, onSuccess}))
    }

    const POST = (
        {
            request, body, handler,
            fileType = false,
            onSuccess = {message: ""}, 
            fromCompany = false,
            meta = {}
        }) => {
            let totalToken = fromCompany ? (companyToken ? companyToken : cookie.companyToken) :
                storeToken ? storeToken : cookie.token;
            return httpPost({
                request: GetCorrectRequest(request),
                body,
                token: storeToken ? storeToken : cookie.token,
                ms: meta.ms,
                fileType,
            })
                .then(result => InnerHandler({result, handler, meta, onSuccess}))
    }

    const onGetUserInfo = () => {
        return GET({request: "users/user/"})
    }

    const PROPS = {
        REQUEST: {
            GET,
            POST,
            CREATED: {
                EXIT: onExit,
                GET_USER_INFO: onGetUserInfo,
                SET_ADMIN_ROLE: SetAdminRole,
            },
            VALUES: {
                ROLE: cookie.role,
                STATUS: cookie.status,
                TOKEN: cookie.token,
                COMPANY_TOKEN: cookie.companyToken,
            }
        },
    }


    if (typeof children === 'function') {
        return children(PROPS)
    }

    return PROPS


}


export default RequestWrapper;