const LeftMenuItem = ({active, name, onClick, link}) => {
    const onClickLocal = useCallback(() => onClick(link), [onClick, link])
    return <div
        onClick={onClickLocal}
        // onClick={onClick}
        className={cn(styles.left_menu_item,
            active && styles.active_left_menu_item)}
    >
        {name}
    </div>
}


