const GetHeaderItemName = ({name, link, profile, isProfile = false}) => {
    if (isProfile) {
        return profile
    }
    return name
}


function ComplexMenu({onClick, path, history, children}) {

    // const elements = role === ROLES.TEACHER ? teacherElements :
    //     (role === ROLES.ADMIN ? adminElements : studentElements);

    const user = useSelector(state => state.user)
    // const [headerTitle, setHeaderTitle] = useState("ComplexMenu title")
    // const [headerSubTitle, setHeaderSubTitle] = useState("Sub title header description")

    const [profile, setProfile] = useState("")
    const [currentLeftIndex, setCurrentLeftIndex] = useState(-1)
    const [secondMenu, setSecondMenu] = useState([])

    const headerTitle = useSelector(state => state.header.title)
    const headerSubTitle = useSelector(state => state.header.subTitle)
    const backTitle = useSelector(state => state.header.backTitle)
    const backLink = useSelector(state => state.header.backLink)

    const pathVars = useRef({
        var1: "",
        var2: "",
        var3: "",
    })

    useEffect(() => {
        try {
            let profileName = ""
            // console.log("PROFILE:", user)
            if (user.last_name) {
                let length = user.last_name.length
                if (length > 15) {
                    setProfile(user.last_name.slice(0, 15) + "...")
                    return
                }
                profileName = user.last_name
                if (user.first_name) {
                    profileName = `${user.last_name} ${user.first_name.charAt(0)}.`
                }
            }
            setProfile(profileName)
        } catch (e) {
            // ...
        }
    }, [user])

    const onClickRightMenu = useCallback((link) => {
        let totalLink = link
            .replace(":VAR_1", pathVars.current.var1)
            .replace(":VAR_2", pathVars.current.var2)
            .replace(":VAR_3", pathVars.current.var3)
        window.scroll(0, 0);
        // console.log("ON MENU TOTAL LINK:", totalLink)
        history.push(totalLink);
    }, [])

    const dispatch = useDispatch();

    const titleKeyWord = useSelector(state => state.header.titleKeyWord)

    useLayoutEffect(() => {
        // console.log("NEW TITLE HEADER KEY WORD:", titleKeyWord)
        if (!titleKeyWord) {
            dispatch(removeHeaderTitles())
        }
    }, [titleKeyWord])

    useLayoutEffect(() => {
        let findFirstItem = false;
        let onClickPath = "";
        let useTitles = false;
        let backWord = "";
        let titleWord = "";
        let useBack = false;
        let menu = [];
        STRUCTURE.map((item, index) => {
            if (findFirstItem) return ""
            // const regRes = item.reg.exec(path);
            const regRes = path.match(item.reg);
            // console.log("TOTAL RES 1:::", regRes)
            const regTrue = !!regRes;
            let totalVarsAmount = 0;
            if (regTrue) {
                onClickPath += item.link;
                findFirstItem = true;
                useTitles = useTitles || item.titles;
                setCurrentLeftIndex(index)
                let currentItem = item;
                let hasMenu = currentItem.menu
                let lastPath = regRes[1]; // path2
                // console.log("|> START HEADER ALG")
                // console.log("|> START HEADER ALG")
                // console.log("|> START HEADER ALG")
                // console.log("LAST PATH:", lastPath)
                let currentRegRes = undefined;
                while (hasMenu) {
                    hasMenu = false;
                    // console.log("CURRENT ITEM:", currentItem)
                    if (currentItem.menu) {
                        hasMenu = true;
                        let findItem = false
                        let nextPathPart = ""
                        let nextCurrentItem = {};
                        let newMenu = currentItem.menu.map((item2, index2) => {
                            if (findItem) return {
                                name: item2.name,
                                active: false,
                                link: onClickPath + item2.link,
                            };
                            let active = false;
                            item2.reg.forEach(reg => {
                                const regRes2 = reg.exec(lastPath);
                                const regTrue2 = !!regRes2;

                                if (regTrue2) {
                                    // console.log("COINCIDENCE:", `LAST PATH: ${lastPath}`, "REG:", reg, "RES:", regRes2)
                                    findItem = true;
                                    nextCurrentItem = item2;
                                    useTitles = useTitles || item2.titles;
                                    useBack = useBack || item2.useBack;
                                    backWord = item2.backWord ? item2.backWord : backWord;
                                    titleWord = item2.titleKeyWord ? item2.titleKeyWord : titleWord;
                                    active = true;
                                    currentRegRes = regRes2;
                                    nextPathPart = item2.link;
                                    if (item2.varsAmount === 1) { // AND >= 1;
                                        lastPath = regRes2[1 + item2.varsAmount]
                                        if (totalVarsAmount === 0) {
                                            pathVars.current.var1 = regRes2[1]
                                        } else if (totalVarsAmount === 1) {
                                            pathVars.current.var2 = regRes2[1]
                                        } else if (totalVarsAmount === 2) {
                                            pathVars.current.var3 = regRes2[1]
                                        }
                                        totalVarsAmount += item2.varsAmount;
                                        // pathVars.current.var1 = regRes[1];
                                    } else {
                                        lastPath = regRes2[1]
                                        // console.log("NEW LAST PATH:", lastPath)
                                    }
                                }
                            })
                            // let active = findSecondItem
                            return {
                                name: item2.name,
                                active: active,
                                link: onClickPath + item2.link,
                            }
                        })
                        currentItem = nextCurrentItem;
                        if (findItem) {
                            onClickPath += nextPathPart;
                            menu = newMenu;
                        }
                        if (typeof lastPath === 'undefined') {
                            break;
                        }
                    }
                }
            }
        })

        setSecondMenu(menu)
        dispatch(setHeaderBackKeyWord({backKeyWord: backWord}))
        dispatch(setHeaderTitleKeyWord({titleKeyWord: titleWord}))

        if (!findFirstItem) {
            setCurrentLeftIndex(-1)
            // setSecondMenu([])
            pathVars.current = {
                var1: "",
                var2: "",
                var3: "",
            }
        }
        if (!useTitles) {
            dispatch(removeHeaderTitles());
        }
        if (!useBack) {
            dispatch(removeHeaderBackInfo())
        }
        // setBackTitle(back);
        // setBackLink(backLink);

    }, [path])

    const onClickLeftMenu = useCallback((link) => {
        history.push(link)
    }, [])


    return <div className={styles.wrapper}>
        <div className={styles.container}>
            <div className={styles.left_container}>
                <LeftMenu/>
            </div>

            <div className={styles.right_container}>
                <HeaderMenu history={history}/>

                <div className={styles.main_content}>
                    {/* <div className={styles.header_inner_block}>
                        <div className={styles.header_inner_block_info}>
                            {backTitle && <div
                                onClick={() => onClickRightMenu(backLink)}
                                className={styles.header_back_info}>
                                <img
                                    alt={"back"}
                                    src={back_ico}
                                    className={styles.profile_img}
                                />
                                <p>{backTitle}</p>
                            </div>}
                            <div className={styles.header_inner_block_titles}>
                                {titleKeyWord.length > 0 && headerTitle && <h6>{headerTitle}</h6>}
                                {titleKeyWord.length > 0 && headerSubTitle && <p>{headerSubTitle}</p>}
                            </div>
                        </div>
                        {secondMenu.length > 0 && <div className={styles.header_inner_block_menu_1}>
                            {secondMenu.map(({name, active, link}, index) =>
                                <div key={index.toString()}
                                     onClick={() => onClickRightMenu(link)}
                                     className={cn(
                                         styles.header_inner_block_menu_item,
                                         active && styles.header_inner_block_menu_item_active,
                                     )}>
                                    <h6>{name}</h6>
                                </div>)}
                        </div>}
                    </div> */}
                    {children}
                </div>
            </div>
        </div>
    </div>;
}


export default ComplexMenu;
