import React, {useEffect, useLayoutEffect, useState} from "react";
import {Redirect, Route, Switch} from 'react-router-dom';
import {PROFILE_URL, MAIN_URL, ORGANIZATIONS_URL, COMPANY_URL, PRODUCT_URL} from "../constants";
import CompaniesPage from "../pages/CompaniesPages/CompaniesPage";
import FactoriesPage from "../pages/CompaniesPages/FactoriesPage";
import ProductsPage from "../pages/CompaniesPages/ProductsPage";
import CompanyPage from "../pages/CompaniesPages/CompanyPage";
import AddCompanyPage from "../pages/CompaniesPages/AddCompanyPage";
import CompanyAddUserPage from "../pages/CompaniesPages/CompanyPage/CompanyAddUserPage";
import CompanyAddProductPage from "../pages/CompaniesPages/ProductsPage/CompanyAddProductPage";
import EditProductPage from "../pages/CompaniesPages/ProductsPage/EditProductPage";
import CompanyProductFiles from "../pages/CompaniesPages/ProductsPage/CompanyProductFiles";
import {useDispatch, useSelector} from "react-redux";
import {setHeaderBackInfo, setHeaderSubTitle, setHeaderTitle} from "../store/actions/header";
import {addCompany} from "../store/actions/user";
import RequestWrapper from "../helpers/RequestWrapper";
import AddFactoryPage from "../pages/CompaniesPages/FactoriesPages/AddFactoryPage";
import CompanyOrdersRouter from "./CompanyOrdersRouter";
import FactoryRouter from "./FactoryRouter";


const GET_REQUEST = "organizations/company/get/"
const PERMISSIONS_REQUEST = "organizations/company/permissions/"


const CompanyRouter = ({match}) => {

    // console.log("|> Company router match:", match)

    const companyId = match.params.company;
    const dispatch = useDispatch()
    const {REQUEST} = RequestWrapper()
    const cacheCompanies = useSelector(state => state.user.companies)
    const titleKeyWord = useSelector(state => state.header.titleKeyWord)
    const backKeyWord = useSelector(state => state.header.backKeyWord)

    useEffect(() => {
        if (titleKeyWord === "company") {
            if (cacheCompanies.findIndex(i => (i.id === companyId && i.name)) < 0) {
                REQUEST.GET({request: `${GET_REQUEST}?id=${companyId}`}).then(res => {
                    if (res.status === 200) {
                        dispatch(setHeaderTitle(res.json.info.name));
                        dispatch(addCompany(res.json.info));
                    }
                })
            } else {
                let name = cacheCompanies.find(i => (i.id === companyId && i.name)).name
                dispatch(setHeaderTitle(name));
            }
        }
    }, [companyId, cacheCompanies, titleKeyWord])

    useEffect(() => {
        if (titleKeyWord === "company") {
            REQUEST.GET({request: `${PERMISSIONS_REQUEST}?id=${companyId}`}).then(res => {
                if (res.status === 200) {
                    let is_member = res.json.is_member;
                    let localRole = is_member ? res.json.role : " ";
                    dispatch(setHeaderSubTitle(localRole));
                    // setPermissions(res.json.permissions);
                }
            })
        }
    }, [companyId, titleKeyWord])

    const START = MAIN_URL.ORGANIZATIONS + ORGANIZATIONS_URL.COMPANIES + "/:company"
    useLayoutEffect(() => {
        if (backKeyWord === "companies") {
            dispatch(setHeaderBackInfo({
                backTitle: "  ",
                backLink: "/organizations/companies",
            }))
        }
    }, [backKeyWord])

    return <Switch>
        <Route exact path={START + COMPANY_URL.INFO}
               component={CompanyPage}/>
        <Route exact path={START + COMPANY_URL.ADD_STAFF}
               component={CompanyAddUserPage}/>
        <Route exact path={START + COMPANY_URL.ADD_PRODUCT}
               component={CompanyAddProductPage}/>
        <Route exact path={START + COMPANY_URL.ADD_FACTORY}
               component={AddFactoryPage}/>
        <Route exact path={START + COMPANY_URL.PRODUCTS_FILES}
               component={CompanyProductFiles}/>
        <Route path={START + COMPANY_URL.ORDERS}
               component={CompanyOrdersRouter}/>
        <Route exact path={START + COMPANY_URL.PRODUCTS + "/:product"}
               component={EditProductPage}/>
        <Route exact path={START + "/:section"}
               component={CompanyPage}/>
        <Route path={START + COMPANY_URL.FACTORIES + "/:factory"}
               component={FactoryRouter}/>
        <Redirect to={MAIN_URL.ORGANIZATIONS}/>
    </Switch>
};


export default CompanyRouter;
