import React, {useEffect, useState} from 'react';
import styles from "./styles.module.scss";
import RequestView from "../../../../components/RequestView";
import AppButton from "../../../../components/ui/AppButton";
import {COMPANY_URL, FACTORY_URL, MAIN_URL, ORGANIZATIONS_URL} from "../../../../constants";
import {AppWhiteBlock} from "../../../../components/ui/AppWhiteBlock";

const GET_REQUEST = "organizations/factory/users/subordinate/"


function FactoryStaffPage (
    {
        match,
        history,
    }) {

    const companyId = match.params.company;
    const factoryId = match.params.factory;

    const onAddStaff = () => {
        history.push(MAIN_URL.ORGANIZATIONS + ORGANIZATIONS_URL.COMPANIES +
            `/${companyId}` + COMPANY_URL.FACTORIES + `/${factoryId}` +
            FACTORY_URL.ADD_STAFF)
    }

    const [q, setQ] = useState("")
    const [loading, setLoading] = useState(false)
    const [users, setUsers] = useState([])
    const onRequest = (res) => {
        console.log("STAFF REQUEST RES:", res)
        if (res.status === 200) {
            setUsers(res.json.users)
            setLoading(false)
        }
    }

    return <RequestView
        // needDoRequest={false}
        request={`${GET_REQUEST}?id=${factoryId}`}
        loading={loading}
        onRequest={onRequest}
    >
        <div className={styles.header_line}>
            <h6> </h6>
            <input type={'search'}
                   placeholder={" ..."}
                   value={q} onChange={(e) =>
                setQ(e.target.value)}/>
            <AppButton onClick={onAddStaff}>
                    
            </AppButton>
        </div>
        <table>
            {/*<caption>  </caption>*/}
            <thead>
            <tr>
                <th></th>
                <th></th>
                {/*<th> </th>*/}
                <th> </th>
                <th> </th>
            </tr>
            </thead>
            <tbody>
            {users
                .filter(user =>
                    user.user.username.includes(q) ||
                    user.user.first_name.includes(q) ||
                    user.user.last_name.includes(q)
                )
                .map((user, index) =>
                    <tr key={user.user.id.toString()}>
                        <td>
                            {index + 1}
                        </td>
                        <td>
                            {user.user.first_name + user.user.last_name ?
                                user.user.first_name + " " + user.user.last_name : user.user.username}
                        </td>
                        {/*<td>*/}
                        {/*    {user.creator.name}*/}
                        {/*</td>*/}
                        <td>
                            {user.user.date_joined}
                        </td>
                        <td>
                            <AppButton inverse>
                                
                            </AppButton>
                        </td>
                    </tr>
                )}
            </tbody>
        </table>
    </RequestView>
}


export default FactoryStaffPage;
