import React, {useCallback, useEffect, useState} from 'react';
import styles from "./styles.module.scss";
import AppModal from "../../ui/AppModal";
import {useDispatch, useSelector} from "react-redux";
import {closeEditFile, closeEditImage, openEditFile, openEditImage} from "../../../store/actions/content";
import AppButton from "../../ui/AppButton";
import RequestWrapper from "../../../helpers/RequestWrapper";
import {falseLoadingForUserData, waitingForUserData} from "../../../store/actions/user";
import {useForm} from "react-hook-form";
import AppInputWrapper from "../../ui/AppInputWrapper";
import download_ico from '../../../assets/media/icons/download.ico';

const EDIT_FILE = "products/file/update/"

function EditFileModal() {

    const {register, handleSubmit, setError,
        setValue, formState: {errors, isValid, isDirty}} = useForm();

    const [modalVisible, setModalVisible] = useState(false)
    const [description, setDescription] = useState("")
    const [name, setName] = useState("")
    const dispatch = useDispatch();
    const file = useSelector(state => state.content.file)
    const {REQUEST} = RequestWrapper();

    const onCloseFile = useCallback(() => {
        document.body.classList.remove("noscroll")
        // setDescriptionError("")
        dispatch(closeEditFile())
    }, [])

    const onSave = (data) => {
        dispatch(waitingForUserData())
        console.log("DATA:", data, {file: file.id})
        REQUEST.POST({
            request: EDIT_FILE,
            body: {
                file: file.id,
                ...data,
            },
            onSuccess: {message: " ."}
        }).then(res => {
            dispatch(falseLoadingForUserData())
            if (res.status === 200) {
                // onCloseImage()
                dispatch(openEditFile({...file, ...data}))
            } else {
                let requestErrors = res.json
                if (typeof requestErrors === 'object') {
                    for (let key in requestErrors) {
                        setError(key, {
                            type: 'custom',
                            message: requestErrors[key]
                        })
                    }
                }
            }
        })
    }

    useEffect(() => {
        if (file.link) {
            document.body.classList.add("noscroll")
            // setDescription(file.description)
            setValue("name", file.name)
            setValue("description", file.description)
            setModalVisible(true)
        }
    }, [file])

    return <AppModal
        modalVisible={modalVisible}
        setModalVisible={setModalVisible}
        onClose={onCloseFile}
        showLoading={false}
        showWhiteBlock={true}
        canCloseOnBackdrop={false}>
        <AppButton inverse onClick={() => window.open(file.link)}>
            <img
                alt={"download"}
                src={download_ico}
                className={styles.download_ico}
            />
             
        </AppButton>
        <form
            className={styles.form}
            onSubmit={handleSubmit(onSave)}>
        <h5> </h5>
            <AppInputWrapper
                label={" ( 50 )"}
                errors={errors}
                name={"name"}>
                <input {...register("name",
                    { required: " " }
                )}
                       placeholder={" "} type={"text"} />
            </AppInputWrapper>
            <AppInputWrapper
                label={" ( 300 )"}
                errors={errors}
                name={"description"}>
                <textarea
                    {...register("description")}
                       placeholder={" "}/>
            </AppInputWrapper>
            {/*<div className={styles.buttons}>*/}
            <input type="submit" value={""}/>
            {/*</div>*/}
        </form>
    </AppModal>;
}


export default EditFileModal;
