import React, {useEffect, useLayoutEffect, useState} from 'react';
// import styles from "./styles.module.scss";
import RequestView from "../../../../components/RequestView";
import {useSelector} from "react-redux";
import RequestWrapper from "../../../../helpers/RequestWrapper";
import {useForm} from "react-hook-form";
import {
    COMPANY_TYPE_LIST,
    COMPANY_URL,
    FACTORY_URL,
    MAIN_URL,
    ORGANIZATIONS_URL,
    PRODUCT_URL
} from "../../../../constants";
import AppInputWrapper from "../../../../components/ui/AppInputWrapper";

const GET_COMPANY_REQUEST = "organizations/factory/get/"
const ADD_PRODUCT_REQUEST = "products/product/create/"


function FactoryAddProductPage (
    {
        history,
        match,
    }) {

    const companyId = match.params.company;
    const factoryId = match.params.factory;

    const {register, handleSubmit, setError,
        setValue, formState: {errors, isValid, isDirty}} = useForm();

    const cacheFactories = useSelector(state => state.user.factories)
    const [needDoRequest, setNeedDoRequest] = useState(
        cacheFactories.findIndex(i => (i.id === factoryId && i.name)) < 0)
    const [loading, setLoading] = useState(true)
    const [name, setName] = useState("")
    const {REQUEST} = RequestWrapper();

    useLayoutEffect(() => {
        setNeedDoRequest(cacheFactories.findIndex(i => (i.id === factoryId && i.name)) < 0)
    }, [factoryId, cacheFactories])

    useLayoutEffect(() => {
        if (!needDoRequest) {
            try {
                setName(cacheFactories.find(i => i.id === factoryId).name)
            } catch (e) {
                //
            }
        }
    }, [needDoRequest])

    const onFactoryRequest = (res) => {
        // console.log("PRODUCTS::::", res.json)
        // setProducts(res.json.list)
        setName(res.json.info.name)
        setLoading(false)
    }

    const onSubmit = (info) => {
        REQUEST.POST({
            request: ADD_PRODUCT_REQUEST,
            body: {
                factory: factoryId,
                ...info,
            }
        }).then(res => {
            // console.log("|>> Res add Product:", res)
            if (res.status === 200) {
                const productId = res.json.id;
                // console.log("GET PATH: ", MAIN_URL.ORGANIZATIONS + ORGANIZATIONS_URL.COMPANIES +
                //     `/${companyId}` + COMPANY_URL.FACTORIES + `/${factoryId}`
                //     + FACTORY_URL.PRODUCTS + `/${productId}`)
                history.push(MAIN_URL.ORGANIZATIONS + ORGANIZATIONS_URL.COMPANIES +
                    `/${companyId}` + COMPANY_URL.FACTORIES + `/${factoryId}`
                    + FACTORY_URL.PRODUCTS + `/${productId}`)
            } else {
                for (let key in res.json) {
                    setError(key, {
                        type: 'custom',
                        message: res.json[key]
                    })
                }
            }
        })
    }

    return <RequestView
        needDoRequest={needDoRequest}
        request={`${GET_COMPANY_REQUEST}?id=${factoryId}`}
        loading={loading}
        onRequest={onFactoryRequest}
    >
        <form onSubmit={handleSubmit(onSubmit)}>
            <h2> </h2>
            <p>        </p>
            <AppInputWrapper
                label={""}
                errors={errors}
                name={"factory"}>
                <input type={"text"} value={name}
                       disabled={true}/>
            </AppInputWrapper>
            <AppInputWrapper
                label={" "}
                errors={errors}
                name={"name"}>
                <input type={"text"} {...register("name",
                    { required: " " })}
                />
            </AppInputWrapper>
            <AppInputWrapper
                label={""}
                errors={errors}
                name={"price"}>
                <input type={"text"} {...register("price",
                    {
                        required: " ",
                        valueAsNumber: true,
                    })}
                />
            </AppInputWrapper>
            <input type={"submit"} value={""}/>
        </form>
    </RequestView>
}

export default FactoryAddProductPage;
