import React, {useState} from 'react';
// import styles from "./styles.module.scss";
import {falseLoadingForUserData, waitingForUserData} from "../../../../store/actions/user";
import {useDispatch} from "react-redux";
import RequestWrapper from "../../../../helpers/RequestWrapper";
import {COMPANY_URL, FACTORY_URL, MAIN_URL, ORGANIZATIONS_URL} from "../../../../constants";
import AddCompanyUserForm from "../../../../containers/Forms/AddCompanyUserForm";
import RequestView from "../../../../components/RequestView";
import {addNotification} from "../../../../store/actions/content";
import {AppWhiteBlock} from "../../../../components/ui/AppWhiteBlock";
import AppButton from "../../../../components/ui/AppButton";
import AddFactoryUserForm from "../../../../containers/Forms/AddFactoryUserForm";

const GET_REQUEST = "organizations/factory/get/"
const GET_COMPANY_STAFF = "organizations/company/users/subordinate/"


function FactoryAddUserPage (
    {
        history,
        match
    }) {

    const companyId = match.params.company;
    const factoryId = match.params.factory;

    const onAdd = () => {
        history.push(MAIN_URL.ORGANIZATIONS + ORGANIZATIONS_URL.COMPANIES +
            `/${companyId}` + COMPANY_URL.FACTORIES + `/${factoryId}` + FACTORY_URL.STAFF)
    }

    const [users, setUsers] = useState([]);
    const [loading, setLoading] = useState(true);
    // const [name, setName] = useState("");

    const onRequest = (res) => {
        setUsers(res.json.users.map(user => ({
            key: user.user.id,
            name: `${user.user.first_name} ${user.user.last_name} (${user.user.username})`})))
        // setName(res.json.info.name)
        setLoading(false)
    }

    const {REQUEST} = RequestWrapper();
    const dispatch = useDispatch();

    const [errors, setErrors] = useState({})
    const [error, setError] = useState("")

    const onAddStaff = (body) => {
        dispatch(waitingForUserData())
        REQUEST.POST({
            request: "organizations/factory/users/add/",
            body: {...body, id: factoryId},
            onSuccess: {message: " "}
        }).then(res => {
            dispatch(falseLoadingForUserData())
            try {
                console.log("|>>>>> NEW STAFF USERS:", res.json)
                if (res.status === 200) {
                    onAdd()
                    // setUsers(res.json)
                } else {
                    setErrors(res.json)
                }
            } catch (e) {
                setError(":" + e.toString())
                console.log("ERR GET COMPANY:", e);
            }
        })
    };

    return <RequestView
        request={`${GET_COMPANY_STAFF}?id=${companyId}`}
        loading={loading}
        onRequest={onRequest}
    >
        <AddFactoryUserForm
            onSubmit={onAddStaff}
            requestErrors={errors}
            error={error}
            users={users}
        />
    </RequestView>
}

export default FactoryAddUserPage;
