import {
    ADD_NOTIFICATION,
    DELETE_NOTIFICATION,
    DELETE_ALL_NOTIFICATIONS,
    OPEN_EDIT_IMAGE,
    CLOSE_EDIT_IMAGE, OPEN_EDIT_FILE, CLOSE_EDIT_FILE
} from "../types";

const initialState = {
    notifications: [],
    loading: false,
    error: false,
    image: {
        link: "",
        description: "",
        id: 0,
    },
    file: {
        link: "",
        name: "",
        description: "",
        id: 0,
    }
};

export const ContentReducer = (state = initialState, action) => {
    switch (action.type) {
        case ADD_NOTIFICATION:
            return {
                ...state,
                notifications: [...state.notifications, action.notification],
                loading: false,
                error: false,
            };
        case DELETE_NOTIFICATION:
            return {
                ...state,
                notifications: state.notifications.filter(n => n.id !== action.notification.id),
                loading: false,
                error: false,
            };
        case DELETE_ALL_NOTIFICATIONS:
            return {
                ...state,
                notifications: [],
                loading: false,
                error: false,
            };
        case OPEN_EDIT_IMAGE:
            return {
                ...state,
                image: action.payload
            }
        case OPEN_EDIT_FILE:
            return {
                ...state,
                file: action.payload
            }
        case CLOSE_EDIT_IMAGE:
            return {
                ...state,
                image: {
                    link: "",
                    description: "",
                    id: 0,
                },
            }
        case CLOSE_EDIT_FILE:
            return {
                ...state,
                file: {
                    link: "",
                    description: "",
                    name: "",
                    id: 0,
                },
            }
        default:
            return state;
    }
};
