import React, {useEffect, useState} from 'react';
import styles from "./styles.module.scss";
import RequestView from "../../../../../components/RequestView";
import {COMPANY_URL, MAIN_URL, ORGANIZATIONS_URL} from "../../../../../constants";
import RequestWrapper from "../../../../../helpers/RequestWrapper";
import {useDispatch} from "react-redux";
import {falseLoadingForUserData, waitingForUserData} from "../../../../../store/actions/user";
import CompanyInformationForm from "../../../../../containers/Forms/CompanyInformationForm";
import {addNotification} from "../../../../../store/actions/content";

const GET_REQUEST = "organizations/company/get/"

function CompanyInfoSection(
    {
        companyId,
        data = {},
        onSave = () => {},
        editable=false,
    }) {

    const [loading, setLoading] = useState(false);
    // const onSave = (data) => {
    //     history.push(MAIN_URL.ORGANIZATIONS + ORGANIZATIONS_URL.COMPANIES +
    //         `/${data.id}` + COMPANY_URL.INFO)
    // }

    const {REQUEST} = RequestWrapper();
    const dispatch = useDispatch();

    const [errors, setErrors] = useState({})
    const [error, setError] = useState("")

    const onUpdateCompany = (body) => {
        dispatch(waitingForUserData())
        REQUEST.POST({
            request: "organizations/company/update/",
            body: {...body, id: companyId},
            onSuccess: {message: " !"},
        }).then(res => {
            dispatch(falseLoadingForUserData())
            try {
                console.log("|>>>>> NEW CompanyInfo", res.json)
                if (res.status === 200) {
                    // onSave(res.json)
                    onSave(body)
                } else {
                    setErrors(res.json)
                }
            } catch (e) {
                setError(":" + e.toString())
                console.log("ERR GET COMPANY:", e);
            }
        })
    };

    // return <div>
    //     ggg
    // </div>

    return <CompanyInformationForm
        onSubmit={onUpdateCompany}
        requestErrors={errors}
        editable={editable}
        error={error}
        data={data}
        headerText={"  "}
        buttonText={""}
    />
}

export default CompanyInfoSection;
