function ProfileForm(
    {
        onSubmit = () => {
        },
        loading = false,
        requestErrors = {},
        data = {},
    }) {

    const {register, handleSubmit, setError,
        setValue, formState: {errors, isValid, isDirty}} = useForm();

    //const [error, setError] = useState("")

    // useEffect(() => {
    //     const hasKeys = !!Object.keys(errors).length;
    //     if (hasKeys) {
    //         setError("   ")
    //     } else {
    //         setError("")
    //     }
    // }, [errors])

    useEffect(() => {
        if (typeof requestErrors === 'object') {
            for (let key in requestErrors) {
                setError(key, {
                    type: 'custom',
                    message: requestErrors[key]
                })
            }
        }
    }, [requestErrors])

    useEffect(() => {

        for (let key in data) {
            setValue(key, data[key]);
        }
    }, [data])

    const onLocalSubmit = useCallback((data) => {
        console.log("LOCAL SUBMIT:", data)
        onSubmit(data)
    }, [onSubmit])

    const dataArray = [
        {
            name: "first_name",
            type: "text",
            label: "",
            placeholder: " ...",
            error: errors.first_name,
        },
        {
            name: "last_name",
            type: "text",
            label: "",
            placeholder: " ...",
            error: errors.last_name,
        },
        {
            name: "middle_name",
            type: "text",
            label: "",
            placeholder: " ...",
            error: errors.middle_name,
        },
        {
            name: "email",
            type: "text",
            label: "Email",
            placeholder: " email...",
            error: errors.email,
        },
        {
            name: "country",
            type: "text",
            label: "",
            placeholder: " ...",
            error: errors.country,
        },
        {
            name: "city",
            type: "text",
            label: "",
            placeholder: " ...",
            error: errors.city,
        },
    ]


    return <form onSubmit={handleSubmit(onLocalSubmit)}>
        <h2></h2>

        {dataArray.map((
            {
                name,
                label,
                type = "text",
                placeholder,
                error
            }, index) =>
            <AppInputWrapper
                key={index.toString()}
                label={label}
                errors={errors}
                name={name}>
                <input {...register(name,
                    //{ required: "Allo!!" }
                )}
                       placeholder={placeholder} type={type} />
            </AppInputWrapper>
        )}
        <input type="submit" value={""} />
    </form>
}

export default ProfileForm;
