import React, {useCallback, useEffect, useLayoutEffect, useState} from 'react';
import styles from "./styles.module.scss";
import RequestView from "../../../../components/RequestView";
import {useDispatch, useSelector} from "react-redux";
import RequestWrapper from "../../../../helpers/RequestWrapper";
import {useForm} from "react-hook-form";
import {COMPANY_TYPE_LIST, TG_BOT_URL} from "../../../../constants";
import AppInputWrapper from "../../../../components/ui/AppInputWrapper";
import trash_ico from '../../../../assets/media/icons/trash.ico';
import document_ico from '../../../../assets/media/icons/document.ico';
import download_ico from '../../../../assets/media/icons/download.ico';
import edit_ico from '../../../../assets/media/icons/edit.ico';
// import trash_ico from '../../../../assets/media/icons/trash.ico';
import {falseLoadingForUserData, waitingForUserData} from "../../../../store/actions/user";
import cn from "classnames";
import {openEditFile, openEditImage} from "../../../../store/actions/content";
import AppModal from "../../../../components/ui/AppModal";
import AppButton from "../../../../components/ui/AppButton";
import AddFieldForm from "../../../../containers/Forms/AddFieldForm";


const GET_PRODUCT_REQUEST = "products/product/update/get/"
const ADD_IMAGE = "products/product/update/image/add/"
const ADD_FILE = "products/product/update/file/add/"
const SET_MAIN_IMAGE = "products/product/update/image/main/"
const DELETE_IMAGE = "products/product/update/image/delete/"
const DELETE_FILE = "products/product/update/file/delete/"
const UPDATE_PRODUCT = "products/product/update/"


const validFileType = (file) => {
    return true;
    let fileTypes = [
        'image/jpeg',
        'image/pjpeg',
        'image/png',

    ]

    for (let i = 0; i < fileTypes.length; i++) {
        if (file.type === fileTypes[i]) {
            return true;
        }
    }

    return false;
}


function EditProductPage(
    {
        history,
        match,
    }) {

    const productId = match.params.product

    const editImage = useSelector(state => state.content.image)
    const editFile = useSelector(state => state.content.file)

    const [images, setImages] = useState([])
    const [mainImageId, setMainImageId] = useState(-1)
    const [imageError, setImageError] = useState("")

    const [files, setFiles] = useState([])
    const [fileError, setFileError] = useState("")

    const [meta, setMeta] = useState([])
    const [addFieldModal, setAddFieldModal] = useState(false)

    const {
        register, handleSubmit, setError,
        setValue, formState: {errors, isValid, isDirty}
    } = useForm();

    const onAddFieldSubmit = (data) => {
        let index = meta.length > 0 ? (Math.max(...meta.map(item => item.id)) + 1) : 1;
        let name = `var${index}`
        let type = data.textarea.length > 0 ? "textarea" : "text";
        setMeta([...meta, {id: index, type, label: data.label, name, }])
        setAddFieldModal(false);
    }

    useEffect(() => {
        console.log("NEW META:", meta)
    }, [meta])

    const onEditImage = useCallback((id) => {
        let index = images.findIndex(image => image.id === id)
        if (index >= 0) {
            console.log("ON EDIT IMAGE:", images[index])
            dispatch(openEditImage(images[index]))
        }
    }, [images])

    const onEditFile = useCallback((id) => {
        let index = files.findIndex(file => file.id === id)
        if (index >= 0) {
            console.log("ON EDIT FILE:", files[index])
            dispatch(openEditFile(files[index]))
        }
    }, [files])

    useEffect(() => {
        try {
            let index = images.findIndex(im => im.id === editImage.id)
            if (index >= 0 && images[index].description !== editImage.description) {
                images[index] = editImage;
                setImages([...images])
            }
        } catch (e) {
            //
        }
    }, [editImage, images])

    useEffect(() => {
        console.log("Update files by editing...")
        try {
            let index = files.findIndex(im => im.id === editFile.id)
            if (index >= 0 &&
                ((files[index].description !== editFile.description &&
                        typeof editFile.description !== 'undefined') ||
                    files[index].name !== editFile.name
                )) {
                files[index] = editFile;
                setFiles([...files])
            }
        } catch (e) {
            //
        }
    }, [editFile, files])

    const [loading, setLoading] = useState(true)

    const {REQUEST} = RequestWrapper();
    const [data, setData] = useState({})

    const onProductRequest = (res) => {
        console.log("PRODUCT REQUEST::::", res.json)
        // setProducts(res.json.list)
        setData(res.json)
        let mainKeys = ["name", "price", "description"]
        mainKeys.forEach(key => {
            setValue(key, res.json[key])
        })
        setImages(res.json.images)
        setFiles(res.json.files)
        setMainImageId(res.json.main_image)
        let new_meta = res.json.meta_information.map(item => ({...item, name: `var${item.id}`}))
        setMeta(new_meta)
        new_meta.forEach(item => {
            setValue(item.name, item.value)
        })
        setLoading(false)
    }

    const onSubmit = (info) => {
        let metaInfo = []
        for (let key in info) {
            let matching = key.match(/^var([0-9]+)$/)
            if (matching) {
                let value = info[key]
                let id = Number(matching[1])
                // console.log("MATCHING:", id, matching)
                let fieldInfo = meta.find(item => item.id === id)
                if (typeof fieldInfo !== 'undefined') {
                    metaInfo.push({
                        id: id,
                        label: fieldInfo.label,
                        type: fieldInfo.type,
                        value: value,
                    })
                }
            }
        }
        REQUEST.POST({
            request: UPDATE_PRODUCT,
            body: {
                id: productId,
                name: info.name,
                description: info.description,
                price: info.price,
                meta_information: JSON.stringify(metaInfo),
            },
            onSuccess: {message: " ."}
        }).then(res => {
            if (res.status !== 200) {
                let requestErrors = res.json
                if (typeof requestErrors === 'object') {
                    for (let key in requestErrors) {
                        setError(key, {
                            type: 'custom',
                            message: requestErrors[key]
                        })
                    }
                }
            }
        })
    }

    const dispatch = useDispatch();

    const onSetMainImage = useCallback((id) => {
        if (id === mainImageId) {
            return;
        }
        dispatch(waitingForUserData())
        REQUEST.POST({
            request: SET_MAIN_IMAGE,
            body: {
                product_id: productId,
                image_id: id,
            }
        }).then(res => {
            dispatch(falseLoadingForUserData())
            if (res.status === 200) {
                setMainImageId(res.json.main_image)
            }
        })
    }, [productId, mainImageId])

    const onDeleteImage = useCallback((id) => {
        dispatch(waitingForUserData())
        REQUEST.POST({
            request: DELETE_IMAGE,
            body: {
                id: id,
            }
        }).then(res => {
            dispatch(falseLoadingForUserData())
            if (res.status === 200) {
                if (res.json.main_image) {
                    setMainImageId(res.json.main_image)
                }
                setImages((im) => im.filter(image => image.id !== id))
            }
        })
    }, [])

    const onDeleteFile = useCallback((id) => {
        dispatch(waitingForUserData())
        REQUEST.POST({
            request: DELETE_FILE,
            body: {
                id: id,
            }
        }).then(res => {
            dispatch(falseLoadingForUserData())
            if (res.status === 200) {
                setFiles((f) => f.filter(file => file.id !== id))
            }
        })
    }, [])

    const onUploadImage = (e) => {
        setImageError("")
        let files = e.target.files;
        if (images.length >= 10) {
            setImageError("      10")
        } else if (files.length >= 0) {
            let file = files[0];
            console.log("SET FILE:", file)
            if (validFileType(file)) {
                if (file.size > 5 * 1024 * 1024) {
                    setImageError("     5 MB")
                } else {
                    dispatch(waitingForUserData())
                    let data = new FormData()
                    data.append("file", file)
                    data.append("product", productId)
                    REQUEST.POST({
                        request: `${ADD_IMAGE}`, //`${ADD_IMAGE}${productId}`,
                        body: data,
                        fileType: true,
                    }).then(res => {
                        console.log("IMAGE UPLOAD RES:", res)
                        dispatch(falseLoadingForUserData())
                        if (res.status === 200) {
                            setImages([...images, res.json])
                            if (res.json.main) {
                                setMainImageId(res.json.id)
                            }
                        } else {
                            setImageError(res.json.detail)
                        }
                        // console.log("IMAGE UPLOAD RES:", res)
                    })
                }
            } else {
                setImageError("  ")
            }
        }
        document.getElementById("image_upload").value = "";
    }

    const onUploadFile = (e) => {
        setFileError("")
        let targetFiles = e.target.files;
        if (files.length >= 5) {
            setFileError("      5")
        } else if (targetFiles.length >= 0) {
            let file = targetFiles[0];
            // console.log("SET FILE:", file)
            if (true) {
                if (file.size > 20 * 1024 * 1024) {
                    setFileError("     20 MB")
                } else {
                    dispatch(waitingForUserData())
                    let data = new FormData()
                    data.append("file", file)
                    data.append("product", productId)
                    REQUEST.POST({
                        request: `${ADD_FILE}?product_id=${productId}`, //`${ADD_IMAGE}${productId}`,
                        body: data,
                        fileType: true,
                    }).then(res => {
                        console.log("FILE UPLOAD RES:", res)
                        dispatch(falseLoadingForUserData())
                        if (res.status === 200) {
                            setFiles(f => [...f, res.json])
                        } else {
                            setFileError(res.json.detail)
                        }
                        // console.log("IMAGE UPLOAD RES:", res)
                    })
                }
            } else {
                setFileError("  ")
            }
        }
        document.getElementById("file_upload").value = "";
    }

    const onDeleteMetaField = useCallback((id) => {
        setMeta((m) => m.filter(field => field.id !== id))
    }, [])

    return <RequestView
        // needDoRequest={false}
        request={`${GET_PRODUCT_REQUEST}?id=${productId}`}
        loading={loading}
        onRequest={onProductRequest}
    >
        <AppModal modalVisible={addFieldModal}
                  setModalVisible={setAddFieldModal}
                  showWhiteBlock={true}
        >
            <AddFieldForm onSubmit={onAddFieldSubmit}/>
        </AppModal>
        <form
            className={styles.form}
            onSubmit={handleSubmit(onSubmit)}>
            <h4> </h4>
            <h6> </h6>
            <AppInputWrapper
                label={""}
                errors={errors}
                name={"name"}>
                <input type={"text"} {...register("name",
                    {required: " "})}
                />
            </AppInputWrapper>
            <AppInputWrapper
                label={", "}
                errors={errors}
                name={"price"}>
                <input type={"text"} {...register("price",
                    {
                        required: " ",
                        valueAsNumber: true,
                    })}
                />
            </AppInputWrapper>
            <AppInputWrapper
                label={""}
                errors={errors}
                name={"description"}>
                <textarea {...register("description",
                    {})}
                />
            </AppInputWrapper>
            <h6> </h6>
            {meta.map((item) =>
                <div
                    className={styles.field_container}
                    key={item.id.toString()}
                >
                <AppInputWrapper
                    label={item.label}
                    errors={errors}
                    name={item.name}>
                    {
                        item.type === "textarea" ?
                            <textarea {...register(item.name,
                                {
                                    required: "    ",
                                })}
                            /> :
                            <input type={"text"} {...register(item.name,
                                {
                                    required: "    ",
                                })}
                            />}
                </AppInputWrapper>
                    <div
                        onClick={() => onDeleteMetaField(item.id)}
                        className={styles.field_trash}>
                        <img
                            alt={"trash"}
                            src={trash_ico}
                            className={styles.field_trash_ico}
                        />
                    </div>
                </div>
            )}
            <AppButton
                inverse
                onClick={() => setAddFieldModal(true)}
            >
                 
            </AppButton>
            <h6> </h6>
            <div className={styles.images}>
                {images.map(({id, description, link}, index) =>
                    <div key={id.toString()}
                         className={styles.image_container}>
                        <img
                            src={link}
                            alt={"product"}/>
                        <div
                            onClick={() => onEditImage(id)}
                            className={cn(styles.edit_picture)}>
                            <p></p>
                        </div>
                        <div
                            onClick={() => onDeleteImage(id)}
                            className={styles.delete_button}>
                            <img
                                alt={"trash"}
                                src={trash_ico}
                                className={styles.close_img}
                            />
                        </div>
                        {/*{id === mainImageId && <div>*/}
                        {/*    */}
                        {/*</div>}*/}
                        <div
                            onClick={() => onSetMainImage(id)}
                            className={cn(
                                id === mainImageId ? styles.main_picture :
                                    styles.do_main_button,)}>
                            <p>{id === mainImageId ?
                                " " : " "}</p>
                        </div>
                    </div>
                )}
            </div>
            <div className={styles.upload_container}>
                <label className={styles.upload_label}
                       htmlFor={"image_upload"}>
                     
                </label>
                <input id={"image_upload"}
                       name={"image_upload"}
                       accept="image/*"
                       onChange={onUploadImage}
                       type={"file"} //value={" "}
                />
                <p>
                       10 .       5 MB.
                </p>
            </div>
            <p className={styles.error}>{imageError}</p>

            <h6> </h6>
            <div className={styles.files}>
                {files.map(({id, name, description, link}, index) =>
                    <div key={id.toString()}
                         className={styles.file_container}>
                        <img
                            alt={"doc"}
                            src={document_ico}
                            className={styles.document_ico}
                        />
                        <p>
                            {name}
                        </p>
                        <div
                            onClick={() => onEditFile(id)}
                            // onClick={() => window.open(link)}
                            className={styles.file_download_container}>
                            <img
                                alt={"download"}
                                src={edit_ico}
                                className={styles.download_ico}
                            />
                            <p>
                                
                            </p>
                        </div>
                        <div
                            onClick={() => onDeleteFile(id)}
                            className={styles.file_delete_container}>
                            <img
                                alt={"trash"}
                                src={trash_ico}
                                className={styles.delete_file_ico}
                            />
                            <p>
                                
                            </p>
                        </div>
                        {/*<img*/}
                        {/*    src={link}*/}
                        {/*    alt={"product"}/>*/}
                        {/*<div*/}
                        {/*    onClick={() => onDeleteImage(id)}*/}
                        {/*    className={styles.delete_button}>*/}
                        {/*    <img*/}
                        {/*        alt={"trash"}*/}
                        {/*        src={trash_ico}*/}
                        {/*        className={styles.close_img}*/}
                        {/*    />*/}
                        {/*</div>*/}
                        {/*/!*{id === mainImageId && <div>*!/*/}
                        {/*/!*    *!/*/}
                        {/*/!*</div>}*!/*/}
                        {/*<div*/}
                        {/*    onClick={() => onSetMainImage(id)}*/}
                        {/*    className={cn(*/}
                        {/*        id === mainImageId ? styles.main_picture :*/}
                        {/*            styles.do_main_button,)}>*/}
                        {/*    <p>{id === mainImageId ?*/}
                        {/*        " " : " "}</p>*/}
                        {/*</div>*/}
                    </div>
                )}
            </div>
            <div className={styles.upload_container}>
                <label className={styles.upload_label}
                       htmlFor={"file_upload"}>
                     
                </label>
                <input id={"file_upload"}
                       name={"file_upload"}
                       accept=".doc,.docx,.txt,.pdf,.application/pdf,.xml,application/msword,application/vnd.openxmlformats-officedocument.wordprocessingml.document"
                       onChange={onUploadFile}
                       type={"file"} //value={" "}
                />
                <p>
                       5         ( pdf, docx,
                    doc, txt).       20 MB.
                </p>
            </div>
            <p className={styles.error}>{fileError}</p>

            <input type={"submit"} value={""}/>
        </form>
        {/*<h2>Product {productId}</h2>*/}
    </RequestView>
}

export default EditProductPage;
