import React, {useCallback, useEffect, useRef, useState} from 'react';
import styles from "./styles.module.scss";
import AppInputWrapper from "../../../components/ui/AppInputWrapper";
import {useForm} from "react-hook-form";


function AddFieldForm(
    {
        onSubmit = () => {
        },
        // requestErrors = {},
        // error = "",
        headerText=" ",
        buttonText="",
    }) {

    const {register, handleSubmit, setError,
        setValue, formState: {errors, isValid, isDirty}} = useForm();

    const onLocalSubmit = useCallback((data) => {
        // console.log("LOCAL SUBMIT:", data)
        onSubmit(data)
        setValue("label", "")
        setValue("textarea", "")
    }, [onSubmit])

    const dataArray = [
        {
            name: "label",
            type: "text",
            label: " ",
            options: { required: " " },
            placeholder: " ...",
            error: errors.label,
        },
        {
            name: "textarea",
            type: "checkbox",
            label: "  ",
            placeholder: "  ",
            error: errors.textarea,
        },
    ]

    return <form
        className={styles.form}
        onSubmit={handleSubmit(onLocalSubmit)}>
        <h3>{headerText}</h3>
        {dataArray.map((
            {
                name,
                label,
                options,
                type = "text",
                placeholder
            }, index) =>
            <AppInputWrapper
                key={name}
                label={label}
                type={type}
                errors={errors}
                name={name}>
                {index === 0 ?
                <input
                    {...register(name, options)}
                       placeholder={placeholder} type={type} /> :
                <input
                    value={name}
                    {...register(name, options)}
                       placeholder={placeholder} type={type} /> }
            </AppInputWrapper>
        )}
        <input type="submit" value={buttonText} />
    </form>
}

export default AddFieldForm;
