import React, { useState, useEffect } from "react";
import axios from "axios";
import "../styles/style.scss"
import {ButtonRegistration} from "./button";
import VisibilitySensor from "react-visibility-sensor";
import { CircularProgressbar } from "react-circular-progressbar";
import "react-circular-progressbar/dist/styles.css";
import NAvbar from "./NavBar/Navbar";
import data from './header_d.json'
const src = "./header1.json" // to public path for being able from request

function Header({afterEnter}) {
    
    const [parameters, setProducts] = useState(data);

    // useEffect(() => {
    //     // axios
    //     //     .get(src)
    //     //     .then(data => {
    //     //         setProducts(data.data);
    //     //     })
    //     setProducts(data)
    // }, []);

        return(
            <header className="header">
                <div className="header__top">
                    <a href="/" className="logo">
                        <div className="svg logo__svg"></div>
                        <div className="svg logo-text__svg"></div>
                    </a>
                    <NAvbar />
                </div>
                <div className="header__main">
                    <div className="header__main-left">
                        <div className="header__main-left-text">
                            <div className="header__main-left-title">
                                      
                            </div>
                            <div className="header__main-left-subtext">
                                      3    
                            </div>
                            <ButtonRegistration afterEnter={afterEnter} />
                        </div>
                    </div>
                    {parameters.map((arg, index) =>
                    <div key={index.toString()} className="header__main-right">
                        <div className="header__main-right-query">
                            <div>
                                <div className="header__main-right-requests">  
                                <div className="header__main-right-requests-num">{arg.requests}</div>
                                </div>
                                <div className="header__main-right-prompt">
                                <div>
                                        <div className="header__main-right-num">{arg.review}</div>
                                        <div className="header__main-right-text"> </div>
                                </div>
                                <div>
                                        <div className="header__main-right-num">{arg.executed}</div>
                                        <div className="header__main-right-text"></div>
                                </div>
                                </div>
                            </div>
                            <div className="header__main-right-cirle">
                                <div>
                                    <VisibilitySensor>
                                        {({ isVisible }) => {
                                            const percentage = isVisible ? 65 : 0;
                                            return (
                                                <CircularProgressbar
                                                    value={percentage}
                                                    text={`${percentage}%`}
                                                    styles={{
                                                        root: {},
                                                        path: {
                                                            stroke: '#6266EB',
                                                            transition: 'stroke-dashoffset 2.9s ease 0s',
                                                        },
                                                        trail: {
                                                            stroke: '#EAEAFF',
                                                        },
                                                        text: {
                                                            fill: '#6266EB',
                                                        },
                                                    }}
                                                />
                                                );
                                            }}
                                    </VisibilitySensor>
                                        <div className="header__main-right-sub">
                                            <div className="header__main-right-subtext">
                                                
                                            </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    )}
                </div>
            </header>
        )
}

export default Header;
