import React, {} from 'react';
import styles from "./styles.module.scss";
import build_svg from '../../../../../assets/media/icons/LeftMenu/build.svg';


export const HeaderCompanyItem = ({name, role, image, onClick=()=>{}}) => {

    return <div onClick={onClick}
        className={styles.container}>
        <div className={styles.image_container}>
            {!!image ? <img
                alt={"company"}
                src={image}
                className={styles.image}
            /> : <img
                alt={'company'}
                src={build_svg}
                className={styles.build_image}
            />}
        </div>
        <div className={styles.titles}>
            <h6>{name}</h6>
            <p>{role}</p>
        </div>
    </div>;
}
