const StatisticVariant = (elem, onClick) => {
    return <div onClick={onClick}
                className={cn(styles.stat_elem, elem.active && styles.active_stat_elem)}
                key={elem.key}>
        {elem.name}
    </div>
}


const ErrorMessage = ({error}) => {
    const date = new Date(error.id);
    return <div className={cn(
        styles.request_error,
        (error.code === 200 || error.positive) && styles.green_notification)}>
        <div className={styles.err_message}>
            {error.message + " "}
            {error.positive && <img
                alt={"done"}
                src={done}
                className={styles.done_img}
            />}
        </div>
        {!error.positive && <div className={styles.request_error_time}>
            {
                date.getHours().toString()
            }:
            {
                date.getMinutes().toString().length === 1 ?
                    "0" + date.getMinutes().toString() : date.getMinutes().toString()
            }:
            {
                date.getSeconds().toString().length === 1 ?
                    "0" + date.getSeconds().toString() : date.getSeconds().toString()
            }
        </div>}
    </div>
}


