import {
    ADD_NOTIFICATION,
    DELETE_NOTIFICATION,
    DELETE_ALL_NOTIFICATIONS,
    OPEN_EDIT_IMAGE,
    CLOSE_EDIT_IMAGE, OPEN_EDIT_FILE, CLOSE_EDIT_FILE
} from "../types";

export const addNotification = (notification) => {
    return async (dispatch) => {
        dispatch({
            type: ADD_NOTIFICATION,
            notification
        });
    };
};

export const deleteNotification = (notification) => {
    return async (dispatch) => {
        dispatch({
            type: DELETE_NOTIFICATION,
            notification
        });
    };
};

export const deleteAllNotifications = () => {
    return async (dispatch) => {
        dispatch({
            type: DELETE_ALL_NOTIFICATIONS,
        });
    };
};


export const openEditImage = ({link, description, id}) => {
    return async (dispatch) => {
        dispatch({
            type: OPEN_EDIT_IMAGE,
            payload: {link, description, id}
        });
    };
};


export const closeEditImage = () => {
    return async (dispatch) => {
        dispatch({
            type: CLOSE_EDIT_IMAGE,
            // payload: {link, description, id}
        });
    };
};


export const openEditFile = ({link, description, name, id}) => {
    return async (dispatch) => {
        dispatch({
            type: OPEN_EDIT_FILE,
            payload: {link, description, name, id}
        });
    };
};


export const closeEditFile = () => {
    return async (dispatch) => {
        dispatch({
            type: CLOSE_EDIT_FILE,
        });
    };
};


