import {SET_HEADER_BACK_INFO, SET_HEADER_BACK_KEY_WORD, SET_HEADER_TITLE_KEY_WORD, SET_HEADER_TITLES} from "../types";


export const removeHeaderTitles = () => {
    return async (dispatch) => {
        dispatch({
            type: SET_HEADER_TITLES,
            payload: {title: "", subTitle: ""},
        });
    };
};

export const setHeaderTitle = (title = "") => {
    return async (dispatch) => {
        dispatch({
            type: SET_HEADER_TITLES,
            payload: {title},
        });
    };
};

export const setHeaderSubTitle = (subTitle = "") => {
    return async (dispatch) => {
        dispatch({
            type: SET_HEADER_TITLES,
            payload: {subTitle},
        });
    };
};


export const setHeaderBackInfo = ({backTitle, backLink}) => {
    return async (dispatch) => {
        dispatch({
            type: SET_HEADER_BACK_INFO,
            payload: {backLink, backTitle},
        });
    };
};


export const removeHeaderBackInfo = () => {
    return async (dispatch) => {
        dispatch({
            type: SET_HEADER_BACK_INFO,
            payload: {backLink: "", backTitle: ""},
        });
    };
};


export const removeHeaderBackKeyWord = () => {
    return async (dispatch) => {
        dispatch({
            type: SET_HEADER_BACK_KEY_WORD,
            payload: {backKeyWord: ""},
        });
    };
};

export const setHeaderBackKeyWord = ({backKeyWord}) => {
    return async (dispatch) => {
        dispatch({
            type: SET_HEADER_BACK_KEY_WORD,
            payload: {backKeyWord},
        });
    };
};


export const setHeaderTitleKeyWord = ({titleKeyWord}) => {
    return async (dispatch) => {
        dispatch({
            type: SET_HEADER_TITLE_KEY_WORD,
            payload: {titleKeyWord},
        });
    };
};
