import React, {useEffect, useState} from 'react';
import styles from "./styles.module.scss";
import AppButton from "../../../components/ui/AppButton";
import AppInput from "../../../components/ui/AppInput";
import {AppWhiteBlock} from "../../../components/ui/AppWhiteBlock";
import RequestWrapper from "../../../helpers/RequestWrapper";
import {useDispatch, useSelector} from "react-redux";
import {closeEnterForm} from "../../../store/actions/user";
import {COOKIES_NAMES, MAIN_URL} from "../../../constants";
import {useCookies} from "react-cookie";
import {useForm} from "react-hook-form";
import AppInputWrapper from "../../../components/ui/AppInputWrapper";


function EnterForm({history}) {

    const {register, handleSubmit, setError,
        setValue, formState: {errors, isValid, isDirty}} = useForm();

    // const [login, setLogin] = useState("")
    // const [password, setPassword] = useState("")
    const [error, setLocalError] = useState("")

    const {REQUEST} = RequestWrapper()

    const dispatch = useDispatch();

    const onClose = () => {
        dispatch(closeEnterForm());
        if (history) {
            history.push(MAIN_URL.LANDING);
        }
    }

    const onEnter = () => {
        window.scroll(0, 0);
        console.log("ON ENTER!")
        if (history) {
            history.push(MAIN_URL.ORGANIZATIONS);
        }
        dispatch(closeEnterForm());
    }

    const token = useSelector(state => state.user.token)

    // const [cookies, setCookie] = useCookies([COOKIES_NAMES.TOKEN]);

    const [requestErrors, setRequestErrors] = useState({})

    useEffect(() => {
        if (typeof requestErrors === 'object') {
            for (let key in requestErrors) {
                setError(key, {
                    type: 'custom',
                    message: requestErrors[key]
                })
            }
        }
    }, [requestErrors])

    const onLogin = (d) => {
        console.log("Start auth");
        setLocalError("")
        if (token) {
            onEnter();
            return;
        }
        // setError("");
        // if (login === "" || password === "") {
        //     // setError("  ");
        // } else {
            REQUEST.GET({
                request: `users/auth/?username=${d.login}&password=${d.password}`, meta: {login: true},
                handler: (res) => {
                    console.log("Auth results:", res)
                    if (res.message || res.status !== 200) {
                        setRequestErrors(res.json)
                        try {
                            if (res.json.detail) {
                                setLocalError(res.json.detail)
                            }
                        } catch (e) {
                            //
                        }
                        // setError(": " + res.message);
                    } else {
                        onEnter();
                    }
                }
            })
        // }
    }

    return <form
            className={styles.form}
            onSubmit={handleSubmit(onLogin)}
        >
        <h2>  </h2>

        <p className={styles.errors}>
            {error}
        </p>
        <AppInputWrapper
            label={""}
            errors={errors}
            name={"login"}>
            <input {...register("login", { required: " " })}
                   placeholder={" "} 
                   type={"text"} 
                   />
        </AppInputWrapper>
        <AppInputWrapper
            label={""}
            errors={errors}
            name={"password"}>
            <input {...register("password", { required: " " })}
                   placeholder={" "} type={"password"} />
        </AppInputWrapper>
        <div className={styles.div_center}>

            <input type="submit" value={""}/>
        </div>
        <p onClick={onClose}
           className={styles.close_p}>
              
        </p>
    </form>
}

export default EnterForm;
