import React, {useCallback} from 'react';
import styles from './styles.module.scss';
import close from '../../../assets/media/icons/close.ico';
import cn from "classnames";
import AppActivityIndicator from "../AppActivityIndicator";

const AppModal = (
    {
        modalVisible = false,
        setModalVisible = () => {
        },
        showLoading = false,
        showWhiteBlock = true,
        canCloseOnBackdrop = true,
        whiteBlockStyle,
        onClick,
        onClose = () => {},
        children,
        ...props
    }) => {

    const onCloseLocal = useCallback(() => {
        console.log("CLOSE")
        onClose();
        setModalVisible(false);
    }, [setModalVisible, onClose])

    return <div
        onClick={()=> {
            console.log("PRESS CLOSE");
            if (canCloseOnBackdrop) {
                setModalVisible(false);
            }
        }}
        className={cn(styles.pop_up_background, !modalVisible && styles.hidden)}>
        {showLoading ? <AppActivityIndicator/> :
            showWhiteBlock ?
                <div onClick={e => e.stopPropagation()} 
                    className={cn(styles.pop_up_content, whiteBlockStyle)}>
                    {/* <button className={styles.button_close} onClick={onCloseLocal}>
                        <img alt={"close"}
                             src={close}
                             className={styles.close_img}
                        />
                    </button> */}
                    {children}
                </div> :
                children
        }
    </div>
}

export default AppModal;