import React, {useState} from "react";
import '../styles/ModalVariant.scss'
import MaskedInput from 'react-text-mask'

function Tabs ({
    email, setEmail, password, setPassword, 
    password2, setPassword2, toggleState, onToggleState}) {

    // const [email, setEmail] = ()

    return (
        <div className="main-block">
            <div className="bloc-tabs">
                <button
                    className={toggleState === 1 ? "tabs active-tabs tab1" : "tabs tab1"}
                    onClick={onToggleState}
                >
                    
                </button>
                <button
                    className={toggleState === 2 ? "tabs active-tabs tab2" : "tabs tab2"}
                    onClick={onToggleState}
                    // onClick={() => setToggleState(2)}
                >
                    
                </button>
            </div>
            <div className="content-tabs">
                <div className={toggleState === 1 ? "content  active-content" : "content"}>
                    <div className="phone"></div>
                    {/* <MaskedInput
                        mask={[/[8]/, '(', /[9]/, /\d/, /\d/, ')', ' ', /\d/, /\d/, /\d/, '-', /\d/, /\d/, /\d/, /\d/]}
                        className="in"
                        showMask={true}
                    /> */}
                    <input type="text"
                    className="in" value={email} onChange={(e) => setEmail(e.target.value)}/>
                </div>
                <div className={toggleState === 1 ? "content  active-content" : "content"}>
                    <div className="phone"></div>
                    <input className="in" 
                    type="password" value={password} onChange={(e) => setPassword(e.target.value)}/>
                </div>
                <div className={toggleState === 1 ? "content  active-content" : "content"}>
                    <div className="phone"> ()</div>
                    <input className="in" 
                    type="password" value={password2} onChange={(e) => setPassword2(e.target.value)}/>
                </div>


                <div className={toggleState === 2 ? "content  active-content" : "content"}>
                    <div className="phone"></div>
                    <input className="in" value={email} onChange={(e) => setEmail(e.target.value)}/>
                </div>
                <div className={toggleState === 2 ? "content  active-content" : "content"}>
                    <div className="phone"></div>
                    <input className="in" 
                    type="password" value={password} onChange={(e) => setPassword(e.target.value)}/>
                </div>
            </div>
        </div>
    );
};

export default Tabs