import React, {useCallback, useEffect, useMemo, useState, useRef} from 'react';
import styles from "./styles.module.scss";
import {HeaderCompanyItem} from "./CompanyItem";
import AppModal from "../../../ui/AppModal";
import {useDispatch, useSelector} from "react-redux";
import {setCompanies} from "../../../../store/actions/user";
import AppButton from "../../../ui/AppButton";
import RequestView from "../../../RequestView";
import RequestWrapper from "../../../../helpers/RequestWrapper";
import {COMPANY_ROLE_LIST, COMPANY_TYPE_LIST, COMPANY_URL, MAIN_URL, 
    ORGANIZATIONS_URL} from "../../../../constants";
import AppInputWrapper from "../../../../components/ui/AppInputWrapper";
import {falseLoadingForUserData, waitingForUserData} from "../../../../store/actions/user";
import {useForm} from "react-hook-form";

// const companies_ = [
//     {name: "SberBank", role: " "},
//     {name: "     ", role: "  "},
// ]

const COMPANY_LIST_REQUEST = "organizations/company/list/"

const GetRole = (index) => {
    try {
        return COMPANY_ROLE_LIST.find(i => i.key === index).name
    } catch (e) {
        return index
    }
}


export function HeaderCompany({name, ico, href, history}) {

    const data = undefined;

    const onSave = (data) => {
        // REQUEST.GET({
        //     request: `users/company/token/${data.id}`,
        //     meta: {companyLogin: true},
        // })
        // here
        setModalVisible(false);
        setAddCompanyModal(false);
        history.push(MAIN_URL.ORGANIZATIONS + ORGANIZATIONS_URL.COMPANIES +
            `/${1}` + COMPANY_URL.INFO)
    }

    const {REQUEST} = RequestWrapper();
    const dispatch = useDispatch();

    const [requestErrors, setRequestErrors] = useState({})
    const [totalError, setTotalError] = useState("")

    const onSubmit = (body) => {
        dispatch(waitingForUserData())
        body.okved = [okvedsRef.current[mainOkved]]
        console.log("BODY:", body)
        REQUEST.POST({
            request: "organizations/company/create/",
            body,
        }).then(res => {
            dispatch(falseLoadingForUserData())
            try {
                console.log("|>>>>> NEW CompanyInfo", res.json)
                if (res.status === 200) {
                    REQUEST.GET({request: COMPANY_LIST_REQUEST}).then(r => {
                        if (r.status === 200) {
                            let newCompanies = r.json.company_list
                            dispatch(setCompanies(newCompanies))
                            let ind = newCompanies.findIndex(i => i.id === data.id)
                            setIndex(ind)
                            console.log("SET MAIN NEW COMPANY")
                        }
                    })
                    // onSave(res.json)
                } else {
                    setRequestErrors(res.json)
                }
            } catch (e) {
                setTotalError(":" + e.toString())
                console.log("ERR GET COMPANY:", e);
            }
        })
    };

    const {
        register, handleSubmit, setError,
        setValue, formState: {errors, isValid, isDirty}
    } = useForm();


    useEffect(() => {
        if (typeof requestErrors === 'object') {
            for (let key in requestErrors) {
                setError(key, {
                    type: 'custom',
                    message: requestErrors[key]
                })
            }
        }
    }, [requestErrors])

    useEffect(() => {
        for (let key in data) {
            setValue(key, data[key]);
        }
    }, [data])

    const onLocalSubmit = useCallback((data) => {
        // console.log("LOCAL SUBMIT:", data)
        onSubmit(data)
    }, [onSubmit])

    useEffect(() => {
        if (totalError) {
            // setTotalError(totalError)
            return
        }
        const hasKeys = !!Object.keys(requestErrors).length;
        if (hasKeys) {
            setTotalError("   ")
        } else {
            setTotalError("")
        }
    }, [requestErrors, totalError])

    const dataArray = [
        {
            name: "name",
            type: "text",
            label: "",
            options: {required: " "},
            placeholder: "  ...",
            error: errors.name,
        },
        {
            name: "inn",
            type: "text",
            label: "",
            // options: {
            //     valueAsNumber: true,
            // },
            options: {required: " "},
            placeholder: " ...",
            error: errors.inn,
        },
        // {
        //     name: "address",
        //     type: "text",
        //     label: " ",
        //     placeholder: " ...",
        //     error: errors.address,
        // },
        {
            name: "website",
            type: "text",
            label: " ",
            options: {required: " "},
            placeholder: "https://mycompany.ru",
            error: errors.website,
        },
    ]

    const editable = true;


    const [index, setIndex] = useState(-1)
    const [companyId, setCompanyId] = useState(0)
    const {companies} = useSelector(s => s.user)

    const company = useMemo(() => {
        if (index >= 0) {
            return companies[index]
        }
        return {
            name: " ",
            role: "  "
        }
    }, [index, companies])

    useEffect(() => {
        if (index > 0) {
            setCompanyId(companies[index].id)
        } else {
            setCompanyId(-1)
        }
    }, [index, companies])

    useEffect(() => {
        REQUEST.GET({
            request: `users/company/token/${companyId}`,
            meta: {companyLogin: true},
        }).then(r => {
            if (r.status === 200) {
                onSave()
            }
        })
    }, [companyId])

    const [loading, setLoading] = useState(true)

    const onRequestCompanies = (res) => {
        console.log("|>>>>> COMPANIES GET:", res.json)
        // dispatch(falseLoadingForUserData())
        setLoading(false)
        if (res.status === 200) {
            dispatch(setCompanies(res.json.company_list))
            console.log("SET COMPANIES")
        } else {

        }
    }

    useEffect(() => {
        console.log("|>> MY COMPANIES:", companies)
    }, [companies])

    const [modalVisible, setModalVisible] = useState(false);
    const [addCompanyModal, setAddCompanyModal] = useState(false);

    const onClickAddCompany = useCallback(() => {
            setModalVisible(false);
            setAddCompanyModal(true);
    }, [])

    const onChooseCompany = useCallback((index) => {
        setIndex(index);
        // setModalVisible(false)
    }, [companies])

    useEffect(() => {
        if (index < 0 && companies && companies.length > 0) {
            onChooseCompany(0);
        }
    }, [index])

    const [mainOkved, setMainOkved] = useState("")
    const [okveds, setOkveds] = useState([]);  // okved/search/

    const okvedsRef = useRef({})

    const onSearchOkveds = () => {
        REQUEST.GET({
            request: `organizations/okved/search/?q=${mainOkved}`,
        }).then(res => {
            try {
                // console.log("|>>>>> NEW OKVEDS", res.json)
                if (res.status === 200) {
                    let arr = res.json.results.map(item => ({
                        id: item.id,
                        name: (item.code + " " + item.name).trim()
                    }))
                    for (let i = 0; i < arr.length; i++) {
                        let element = arr[i];
                        // Do something with element
                        // console.log("ITEMS:", element)
                        okvedsRef.current[element.name] = element.id
                    }
                    // arr.forEach((item, i) => okvedsRef.current[item.name] = item.id)
                    setOkveds(arr)

                } else {
                    // setRequestErrors(res.json)
                }
            } catch (e) {
                console.log("ERR GET OKVEDS:", e);
            }
        })
    };

    useEffect(() => {
        // console.log("MAIN OKVED:", mainOkved)
        // console.log("CURRENT:", okvedsRef.current[mainOkved])
        onSearchOkveds()
    }, [mainOkved])


    return <div>
        <HeaderCompanyItem
            onClick={() => setModalVisible(true)}
            {...company}
            role={GetRole(company.role)}
        />
        <AppModal
            modalVisible={addCompanyModal}
            setModalVisible={setAddCompanyModal}
        >
            <form onSubmit={handleSubmit(onLocalSubmit)}>
                <h5>  </h5>
                {dataArray.map((
                {
                    name,
                    label,
                    options,
                    type = "text",
                    placeholder
                }) =>
                    <AppInputWrapper
                        key={name}
                        label={label}
                        errors={errors}
                        name={name}>
                        <input disabled={!editable} {...register(name, options,
                            //{ required: "Allo!!" }
                        )}
                            placeholder={placeholder} type={type}/>
                    </AppInputWrapper>
                )}

                <AppInputWrapper
                    label={" "}
                    errors={errors}
                    name={"company_type"}>
                    <select
                        disabled={!editable}
                        {...register("company_type", {required: " "})}
                    >
                        {COMPANY_TYPE_LIST.map(({key, name}) =>
                            <option value={key} key={key.toString()}>
                                {name}
                            </option>)}
                    </select>
                </AppInputWrapper>

                <AppInputWrapper
                    key={"okved"}
                    label={" "}
                    errors={errors}
                    name={name}>
                    <input 
                        disabled={!editable} 
                        list={"search_list"}
                        value={mainOkved}
                        onChange={(e) => setMainOkved(e.target.value)}
                    //     {...register(name, options,
                    //     //{ required: "Allo!!" }
                    // )}
                        placeholder={" "} 
                        type={"text"}/>
                        <datalist id="search_list">
                            {okveds.map((item, index) => <option
                                // className={styles.search_option}
                                value={item.name}
                                key={index.toString()}>
                                    {item.name}
                                </option>)}
                        </datalist>
                </AppInputWrapper>

                <input type="submit" value={" "}/>
            </form>
        </AppModal>


        <AppModal
            modalVisible={modalVisible}
            setModalVisible={setModalVisible}
            whiteBlockStyle={styles.white_block_modal}
            showLoading={false}
            showWhiteBlock={true}
            canCloseOnBackdrop={true}>
            <RequestView
                loading={loading}
                request={COMPANY_LIST_REQUEST}
                onRequest={onRequestCompanies}
            >
                <div className={styles.list}>
                    {companies.map((item, index) =>
                        <HeaderCompanyItem
                            {...item}
                            key={item.id.toString()}
                            role={GetRole(item.role)}
                            onClick={()=>onChooseCompany(index)}
                        />)}
                    <AppButton
                        onClick={onClickAddCompany}
                        className={styles.add_company_button}>
                         
                    </AppButton>
                </div>
            </RequestView>
        </AppModal>
    </div>
}
