import React, {useEffect, useState} from 'react';
import styles from "./styles.module.scss";
import RequestView from "../../../../components/RequestView";
import AppButton from "../../../../components/ui/AppButton";
import {COMPANY_URL, FACTORY_URL, MAIN_URL, ORGANIZATIONS_URL} from "../../../../constants";

const GET_REQUEST = "products/factory/products/"


function FactoryProductsPage (
    {
        match,
        history,
    }) {

    const factoryId = match.params.factory;
    const companyId = match.params.company;

    const [q, setQ] = useState("")

    const [loading, setLoading] = useState(false)
    const [products, setProducts] = useState([])
    const onRequest = (res) => {
        // console.log("PRODUCTS::::", res.json)
        setProducts(res.json.list)
    }

    const onAddProduct = () => {
        history.push(MAIN_URL.ORGANIZATIONS + ORGANIZATIONS_URL.COMPANIES +
            `/${companyId}` + COMPANY_URL.FACTORIES + `/${factoryId}` + FACTORY_URL.ADD_PRODUCT)
    }

    const onClickProduct = (productId) => {
        history.push(MAIN_URL.ORGANIZATIONS + ORGANIZATIONS_URL.COMPANIES +
            `/${companyId}` + COMPANY_URL.FACTORIES + `/${factoryId}` +
            FACTORY_URL.PRODUCTS + `/${productId}`)
    }

    return <RequestView
        // needDoRequest={false}
        request={`${GET_REQUEST}?id=${factoryId}`}
        loading={loading}
        onRequest={onRequest}
    >
        <div className={styles.header_line}>
            <h6> </h6>
            <input type={'search'}
                   placeholder={"..."}
                   value={q} onChange={(e) =>
                setQ(e.target.value)}
            />
            <div className={styles.buttons}>
                <AppButton onClick={onAddProduct}>
                     
                </AppButton>
                <a onClick={() =>
                    history.push(MAIN_URL.ORGANIZATIONS + ORGANIZATIONS_URL.COMPANIES +
                    `/${companyId}` + COMPANY_URL.FACTORIES + `/${factoryId}` +
                        FACTORY_URL.PRODUCTS_FILES)}>
                      
                </a>
            </div>
        </div>
        <table>
            <tbody>
            {products
                .filter(product =>
                    product.name.includes(q) ||
                    product.price.toString().includes(q) ||
                    product.description.includes(q)
                )
                .map(({id, name, description, price, image}, index) =>
                    <tr key={id.toString()}>
                        <td>
                            <h6>
                                {index + 1}
                            </h6>
                        </td>
                        <td>
                            <div className={styles.image_container}>
                                {image ?
                                    <img src={image}
                                         alt={"product"}/> :
                                    <div className={styles.default_image}/>}
                            </div>
                        </td>
                        <td className={styles.name}>
                            <h6>
                                {name}
                            </h6>
                            {/*<p>*/}
                            {/*    {price ? `${price} RUB` : "-"}*/}
                            {/*</p>*/}
                        </td>
                        <td className={styles.price}>
                            <p>
                                {price ? `${price} RUB` : "-"}
                            </p>
                        </td>
                        {/*<td className={styles.description}>*/}
                        {/*    {description}*/}
                        {/*</td>*/}
                        <td>
                            <AppButton inverse onClick={() => onClickProduct(id)}>
                                
                            </AppButton>
                        </td>
                    </tr>
                )}
            </tbody>
        </table>
    </RequestView>
}

export default FactoryProductsPage;
