import React, {useCallback, useEffect, useLayoutEffect, useState} from 'react';
import styles from "./styles.module.scss";
import RequestView from "../../../../components/RequestView";
import {useDispatch, useSelector} from "react-redux";
import RequestWrapper from "../../../../helpers/RequestWrapper";
import {useForm} from "react-hook-form";
import {COMPANY_TYPE_LIST, MAIN_URL, PRODUCT_URL, SEARCH_URL, TG_BOT_URL} from "../../../../constants";
import AppInputWrapper from "../../../../components/ui/AppInputWrapper";
import trash_ico from '../../../../assets/media/icons/trash.ico';
import document_ico from '../../../../assets/media/icons/document.ico';
import download_ico from '../../../../assets/media/icons/download.ico';
import edit_ico from '../../../../assets/media/icons/edit.ico';
// import trash_ico from '../../../../assets/media/icons/trash.ico';
import {falseLoadingForUserData, waitingForUserData} from "../../../../store/actions/user";
import cn from "classnames";
import {openEditFile, openEditImage} from "../../../../store/actions/content";
import AppButton from "../../../../components/ui/AppButton";
import AddFieldForm from "../../../../containers/Forms/AddFieldForm";
import AppInputShow from "../../../../components/ui/AppInputShow";
import ShowImageModal from "../../../../components/Modals/ShowImageModal";


const GET_PRODUCT_REQUEST = "products/product/get/"


function ProductShowPage(
    {
        history,
        match,
    }) {

    const productId = match.params.product

    const onOrderProduct = () => {
        history.push(MAIN_URL.SEARCH + SEARCH_URL.PRODUCT + `/${productId}` + PRODUCT_URL.CREATE_REQUEST)
    }

    const editImage = useSelector(state => state.content.image)
    const editFile = useSelector(state => state.content.file)

    const [images, setImages] = useState([])
    const [mainImageId, setMainImageId] = useState(-1)
    const [imageError, setImageError] = useState("")

    const [files, setFiles] = useState([])
    const [fileError, setFileError] = useState("")

    const [meta, setMeta] = useState([])

    const [loading, setLoading] = useState(true)

    const {REQUEST} = RequestWrapper();
    const [data, setData] = useState({})

    const onProductRequest = (res) => {
        console.log("PRODUCT REQUEST::::", res.json)
        // setProducts(res.json.list)
        setData(res.json)
        setImages(res.json.images)
        setFiles(res.json.files)

        setMeta(res.json.meta_information)
        setLoading(false)
    }

    const dispatch = useDispatch();

    const onBack = () => {
        history.push(MAIN_URL.SEARCH)
    }

    const [imageModal, setImageModal] = useState(false)
    const [imageShow, setImageShow] = useState({
        link: "",
        description: "",
    })

    return <RequestView
        // needDoRequest={false}
        request={`${GET_PRODUCT_REQUEST}?id=${productId}`}
        loading={loading}
        onRequest={onProductRequest}
    >
        <ShowImageModal
            modalVisible={imageModal}
            setModalVisible={setImageModal}
            onCloseImage={() => setImageShow({
                link: "",
                description: "",
            })}
            {...imageShow}/>
        <div
            className={styles.form}>
            <p
                className={styles.p_on_back}
                onClick={onBack}>
                {"<   "}
            </p>
            <div className={styles.button_order_container}>
                <AppButton onClick={onOrderProduct}>
                      
                </AppButton>
            </div>
            <h6> </h6>
            <AppInputShow
                label={""}
                value={data.name}/>
            <AppInputShow
                label={", "}
                value={data.price}/>
            <AppInputShow
                label={""}
                value={data.description}/>

            <h6> </h6>
            {meta.map(({label, id, value}) =>
                <AppInputShow
                    key={id.toString()}
                    label={label}
                    value={value}/>
            )}

            <h6></h6>
            <div className={styles.images}>
                {images.map(({description, link}, index) =>
                    <div key={index.toString()}
                         className={styles.image_container}>
                        <img
                            src={link}
                            alt={"product"}/>
                        <div
                            onClick={() => {
                                setImageShow({link, description})
                                setImageModal(true)
                            }}
                            className={cn(styles.edit_picture)}>
                            <p></p>
                        </div>
                    </div>
                )}
            </div>

            <h6></h6>
            <div className={styles.files}>
                {files.map(({name, description, link}, index) =>
                    <div key={index.toString()}
                         className={styles.file_container}>
                        <img
                            alt={"doc"}
                            src={document_ico}
                            className={styles.document_ico}
                        />
                        <p>
                            {name}
                        </p>
                        <div
                            // onClick={() => onEditFile(id)}
                            onClick={() => window.open(link)}
                            className={styles.file_download_container}>
                            <img
                                alt={"download"}
                                src={download_ico}
                                className={styles.download_ico}
                            />
                            <p>
                                
                            </p>
                        </div>
                        {/*<div*/}
                        {/*    // onClick={() => onDeleteFile(id)}*/}
                        {/*    className={styles.file_delete_container}>*/}
                        {/*    <img*/}
                        {/*        alt={"trash"}*/}
                        {/*        src={trash_ico}*/}
                        {/*        className={styles.delete_file_ico}*/}
                        {/*    />*/}
                        {/*    <p>*/}
                        {/*        */}
                        {/*    </p>*/}
                        {/*</div>*/}
                    </div>
                )}
            </div>
        </div>
        <div className={styles.button_order_container}>
            <AppButton onClick={onOrderProduct}>
                  
            </AppButton>
        </div>
    </RequestView>
}

export default ProductShowPage;
