import React, {} from "react";
import {Redirect, Route, Switch} from 'react-router-dom';
import {PROFILE_URL, MAIN_URL} from "../constants";
import ProfilePage from "../pages/ProfilePages/ProfilePage";
import NotificationsPage from "../pages/ProfilePages/NotificationsPage";
import PasswordsPage from "../pages/ProfilePages/PasswordsPage";


const ProfileRouter = () => {

    return <Switch>
        <Route exact path={MAIN_URL.PROFILE}
               component={ProfilePage}/>
        <Route exact path={MAIN_URL.PROFILE + PROFILE_URL.MAIN}
               component={ProfilePage}/>
        <Route exact path={MAIN_URL.PROFILE + PROFILE_URL.NOTIFICATIONS}
               component={NotificationsPage}/>
        <Route exact path={MAIN_URL.PROFILE + PROFILE_URL.PASSWORD}
               component={PasswordsPage}/>

        <Redirect to={MAIN_URL.PROFILE}/>
    </Switch>
};


export default ProfileRouter;
