import React, {useEffect, useState} from "react";
import st from '../styles/ModalWindow.module.scss'
import cn from 'classnames'
import Tabs from "./ModalVariant.module";
import '../styles/style.scss'
import RequestWrapper from "../../helpers/RequestWrapper";
import {useDispatch} from "react-redux";
import {falseLoadingForUserData, loadUserData, openEnterForm, waitingForUserData} from "../../store/actions/user";
import {useForm} from "react-hook-form";
import {deleteToken, setErrorToken} from "../../store/actions/token";
import AppModal from '../ui/AppModal'


// <RegistrationForm onEnter={afterEnter}/>

export function RegistrationModal({afterEnter, active, setActive}) {

    const [mainError, setMainError] = useState("")

    const CheckPasswords = () => {
        if (password === password2) {
            setMainError("")
        } else {
            setMainError("  ")
        }
    }

    const [email, setEmail] = useState("")
    const [password, setPassword] = useState("")
    const [password2, setPassword2] = useState("")

    const [toggleState, setToggleState] = useState(1);

    const onToggleState = () => {
        setToggleState(toggleState === 1 ? 2 : 1)
        setEmail("")
        setPassword("")
        setPassword2("")
        setMainError("")
    }

    const {
        register, handleSubmit, setError,
        setValue, formState: {errors, isValid, isDirty}
    } = useForm();

    // useEffect(() => {
    //     setError2("")
    // }, [email, password, password2])

    const {REQUEST} = RequestWrapper();
    const dispatch = useDispatch();

    const onHaveAccount = () => {
        dispatch(deleteToken())
        dispatch(openEnterForm())
    }

    const [requestErrors, setRequestErrors] = useState({})

    useEffect(() => {
        if (typeof requestErrors === 'object') {
            for (let key in requestErrors) {
                setError(key, {
                    type: 'custom',
                    message: requestErrors[key]
                })
            }
        }
    }, [requestErrors])

    const onLogin = (d) => {
        console.log("Start auth");
        setMainError("")
        // if (token) {
        //     onEnter();
        //     return;
        // }
        if (!email || !password) {
            setMainError("    ")
            return
        }
        // setError("");
        // if (login === "" || password === "") {
        //     // setError("  ");
        // } else {
            REQUEST.GET({
                request: `users/auth/?username=${d.email}&password=${d.password}`, meta: {login: true},
                handler: (res) => {
                    console.log("Auth results:", res)
                    if (res.message || res.status !== 200) {
                        setRequestErrors(res.json)
                        try {
                            if (res.status === 404) {
                                setMainError("  .")
                            } else if (res.json.detail) {
                                setMainError(res.json.detail)
                            }
                        } catch (e) {
                            //
                        }
                        // setError(": " + res.message);
                    } else {
                        afterEnter();
                    }
                }
            })
        // }
    }

    const onRegistration = (data) => {
        if (!email || !password || !password2) {
            setMainError("    ")
            return
        }
        console.log("Start registration")
        if (data.password !== data.password2) {
            setMainError("  ")
            setError("password", {
                type: 'custom',
                message: "  "
            })
            setError("password2", {
                type: 'custom',
                message: "  "
            })
            return;
        }
        dispatch(waitingForUserData())
        REQUEST.POST({
            request: "users/auth/",
            body: {
                username: data.email,
                password: data.password,
            },
            meta: {login: true},
        }).then(res => {
            dispatch(falseLoadingForUserData())
            if (res.status !== 200) {
                setRequestErrors(res.json)
                if (res.json.detail) {
                    setMainError(res.json.detail)
                } else {
                    setMainError(": " + "  ");
                }
            } else {
                afterEnter();
                // onEnter();
            }
        })
    }

    const onPressButton = () => {
        if (toggleState === 1) {
            onRegistration({password, password2, email})
        } else {
            onLogin({email, password})
        }
    }

    return <AppModal
        modalVisible={active}
        setModalVisible={setActive}
    >
        <div className={st.title}>
              
        </div>
        <Tabs 
            email={email} setEmail={setEmail}
            password={password} setPassword={setPassword}
            password2={password2} setPassword2={setPassword2}
            toggleState={toggleState} onToggleState={onToggleState}
        />
        <div>{mainError}</div>
        <button className={st.bt} onClick={onPressButton}>
            
        </button>
    </AppModal>

    return (
        <div 
            className={active ? "modal active" : "modal"} 
            onClick={() => setActive(false)}>
            <div className={st.content} onClick={e => e.stopPropagation()}>
                <div className={st.title}>
                      
                    </div>
                <Tabs />
                <button className={st.bt}></button>
                {/* <div className={st.footer}>
                    <div className={st.social}>  </div>
                    <div className={st.site}>
                        <ul className={st.icons}>
                            <li className={cn(st.vk, st.icon)}></li>
                            <li className={cn(st.google, st.icon)}></li>
                            <li className={cn(st.ya, st.icon)}></li>
                        </ul>
                    </div>
                </div> */}
            </div>
        </div>
    );
};
