import React, {useEffect, useLayoutEffect, useRef, useState} from 'react';
import styles from "./styles.module.scss";
import RequestView from "../../../components/RequestView";
import CompanyInfoSection from "./CompanySections/CompanyInfoSection";
import CompanyStaffSection from "./CompanySections/CompanyStaffSection";
import CompanyPartnersSection from "./CompanySections/CompanyPartnersSection";
import CompanyProductsSection from "./CompanySections/CompanyProductsSection";
import CompanyFactoriesSection from "./CompanySections/CompanyFactoriesSection";
import {AppWhiteBlock} from "../../../components/ui/AppWhiteBlock";
import {COMPANY_URL, MAIN_URL, ORGANIZATIONS_URL} from "../../../constants";
import cn from "classnames";
