import {setHeaderSubTitle, setHeaderTitle} from "../../../store/actions/header";

const GET_REQUEST = "organizations/company/get/"
const PERMISSIONS_REQUEST = "organizations/company/permissions/"
const PARTNERS_AMOUNT_REQUEST = "organizations/company/partners/request/amount/"

const COMPANY_SECTION_NAMES = {
    INFO: "INFO",
    STAFF: "STAFF",
    PRODUCTS: "PRODUCTS",
    FACTORIES: "FACTORIES",
    PARTNERS: "PARTNERS",
}

const COMPANY_SECTIONS = {
    INFO: CompanyInfoSection,
    STAFF: CompanyStaffSection,
    PRODUCTS: CompanyProductsSection,
    FACTORIES: CompanyFactoriesSection,
    PARTNERS: CompanyPartnersSection,
}

const CompanySection = (
    {
        section,
        permissions,
        company,
        history,
        companyId,
        setPartnersRequestAmount,
        setCompany,
        ...params
    }) => {
    let editable = permissions.findIndex(p => p === section.permission) >= 0

    if (section.sectionName === COMPANY_SECTION_NAMES.INFO) {
        return <CompanyInfoSection
            editable={editable}
            companyId={companyId}
            data={company}
            onSave={setCompany}
        />
    }
    if (section.sectionName === COMPANY_SECTION_NAMES.STAFF) {
        return <CompanyStaffSection
            editable={editable}
            history={history}
            companyId={companyId}
        />
    }
    if (section.sectionName === COMPANY_SECTION_NAMES.PRODUCTS) {
        return <CompanyProductsSection
            editable={editable}
            history={history}
            companyId={companyId}
        />
    }
    if (section.sectionName === COMPANY_SECTION_NAMES.FACTORIES) {
        return <CompanyFactoriesSection
            editable={editable}
            history={history}
            companyId={companyId}
        />
    }

    if (section.sectionName === COMPANY_SECTION_NAMES.PARTNERS) {
        return <CompanyPartnersSection
            editable={editable}
            setPartnersRequestAmount={setPartnersRequestAmount}
            companyId={companyId}
        />
    }

    return <div/>
}

export const COMPANY_PERMISSIONS = {
    PRODUCT_INTERACTION_PERMISSION: "product_interaction_permission",
    ORDERING_PERMISSION: "ordering_permission",
    COMPANY_INFO_PERMISSION: "company_info_permission",
    FACTORY_PERMISSION: "factory_permission",
    ADD_USERS_PERMISSION: "add_users_permission",
    PROPOSAL_PERMISSION: "proposal_permission",
}

export const FACTORY_PERMISSIONS = {
    PRODUCT_INTERACTION_PERMISSION: "product_interaction_permission",
    CREATE_ORDERS_PERMISSION: "create_orders_permission",
    FACTORY_INFO_PERMISSION: "factory_info_permission",
    ADD_USERS_PERMISSION: "add_users_permission",
    PROPOSAL_PERMISSION: "proposal_permission",
}


const COMPANY_RIGHT_MENU = [
    {
        name: "",
        link: "info",
        section: COMPANY_SECTIONS.INFO,
        sectionName: COMPANY_SECTION_NAMES.INFO,
        default: true,
        permission: COMPANY_PERMISSIONS.COMPANY_INFO_PERMISSION,
    },
    {
        name: "",
        link: "staff",
        section: COMPANY_SECTIONS.STAFF,
        sectionName: COMPANY_SECTION_NAMES.STAFF,
        default: false,
        permission: COMPANY_PERMISSIONS.ADD_USERS_PERMISSION,
    },
    {
        name: "  ",
        link: "products",
        links: ["products", "add/product"],
        section: COMPANY_SECTIONS.PRODUCTS,
        sectionName: COMPANY_SECTION_NAMES.PRODUCTS,
        default: true,
        permission: COMPANY_PERMISSIONS.PRODUCT_INTERACTION_PERMISSION,
    },
    {
        name: "",
        link: "factories",
        section: COMPANY_SECTIONS.FACTORIES,
        sectionName: COMPANY_SECTION_NAMES.FACTORIES,
        default: true,
        permission: COMPANY_PERMISSIONS.FACTORY_PERMISSION,
    },
    {
        name: "",
        link: "partners",
        section: COMPANY_SECTIONS.PARTNERS,
        sectionName: COMPANY_SECTION_NAMES.PARTNERS,
        default: true,
        permission: COMPANY_PERMISSIONS.COMPANY_INFO_PERMISSION,
    }
]


function CompanyPage({history, match}) {

    const companyId = match.params.company;
    const section = match.params.section;
    const [menuIndex, setMenuIndex] = useState(0)

    const [rightMenu, _setRightMenu] = useState(COMPANY_RIGHT_MENU.map(i => i))
    const rightMenuRef = useRef(rightMenu)
    const setRightMenu = (value) => {
        rightMenuRef.current = value;
        _setRightMenu(value);
    }
    const [permissions, setPermissions] = useState([])
    const [role, setRole] = useState("")

    const [loading, setLoading] = useState(true);
    const [name, setName] = useState("");
    const [company, setCompany] = useState({})

    const [partnersRequestAmount, setPartnersRequestAmount] = useState(0)

    const {REQUEST} = RequestWrapper();

    useEffect(() => {
        if (permissions.findIndex(p => COMPANY_PERMISSIONS.COMPANY_INFO_PERMISSION) >= 0) {
            REQUEST.GET({request: `${PARTNERS_AMOUNT_REQUEST}?id=${companyId}`}).then(res => {
                if (res.status === 200) {
                    setPartnersRequestAmount(res.json.amount)
                }
            })
        }
    }, [permissions, companyId])


    useEffect(() => {
        REQUEST.GET({request: `${PERMISSIONS_REQUEST}?id=${companyId}`}).then(res => {
            if (res.status === 200) {
                let is_member = res.json.is_member;
                let localRole = is_member ? res.json.role : " ";
                setRole(localRole);
                // dispatch(setHeaderSubTitle(localRole));
                setPermissions(res.json.permissions);
            }
        })
    }, [companyId, PERMISSIONS_REQUEST])

    useEffect(() => {
        let newMenu = COMPANY_RIGHT_MENU;
        if (permissions.findIndex(p => p === COMPANY_PERMISSIONS.ADD_USERS_PERMISSION) >= 0) {
            newMenu = newMenu.map(i => {
                if (i.link === "staff") {
                    i.default = true
                }
                return i
            })
        }
        setRightMenu(newMenu)
    }, [permissions])

    useLayoutEffect(() => {
        let index = COMPANY_RIGHT_MENU.findIndex(item => item.link === section)
        if (index >= 0) {
            setMenuIndex(index)
        } else {
            window.scroll(0, 0);
            history.push(MAIN_URL.ORGANIZATIONS +
                ORGANIZATIONS_URL.COMPANIES + `/${companyId}/${COMPANY_RIGHT_MENU[0].link}`);
        }
    }, [section])

    // const [mainSection, setMainSection] = useState(<div/>)

    // useLayoutEffect(() => {
    //     let menuPoint = rightMenuRef.current[menuIndex]
    //     let params = {
    //         editable: permissions.findIndex(p => p === menuPoint.permission),
    //     }
    //     if (menuPoint.link === "info") {
    //         params.data = company;
    //     }
    //     // setMainSection(<CompanySection
    //     //     section={menuPoint.sectionName}
    //     //     history={history}
    //     //     companyId={companyId}
    //     //     {...params}
    //     // />)
    // }, [permissions, menuIndex, company, companyId])

    // useEffect(() => {
    //     console.log("MATCH:", match)
    // }, [])

    const dispatch = useDispatch()

    const onRequest = (res) => {
        console.log("RES COMPANY:", res)
        setName(res.json.info.name)
        // dispatch(setHeaderTitle(res.json.info.name));
        setCompany(res.json.info)
        setLoading(false)
        // dispatch(addCompany(res.json.info));
    }

    const onChooseSection = (id) => {
        window.scroll(0, 0);
        history.push(MAIN_URL.ORGANIZATIONS +
            ORGANIZATIONS_URL.COMPANIES + `/${companyId}/${COMPANY_RIGHT_MENU[id].link}`);
    }

    // useEffect(() => {
    //
    // }, [companyId])

    return <RequestView
        request={`${GET_REQUEST}?id=${companyId}`}
        loading={loading}
        needDoRequest={!name}
        onRequest={onRequest}
    >
        <div className={styles.screen}>
            <div className={styles.main_content}>
                {/*<h1> {name}</h1>*/}
                {/*<h1> {section}</h1>*/}
                <CompanySection
                    section={rightMenu[menuIndex]}
                    history={history}
                    companyId={companyId}
                    company={company}
                    setPartnersRequestAmount={setPartnersRequestAmount}
                    setCompany={setCompany}
                    permissions={permissions}
                />
            </div>
            {/*<div className={styles.header}>*/}
            {/*    <AppWhiteBlock className={styles.block_menu}>*/}
            {/*        <div className={styles.menu_name_div}>*/}
            {/*            <h6>{name}</h6>*/}
            {/*            <p>{role}</p>*/}
            {/*        </div>*/}
            {/*        {rightMenu.filter(i => i.default).map((item, index) =>*/}
            {/*            <div*/}
            {/*                className={cn(styles.menu_item,*/}
            {/*                    menuIndex === index && styles.active_menu_item)}*/}
            {/*                key={index.toString()}*/}
            {/*                onClick={() => onChooseSection(index)}>*/}
            {/*                {item.name}*/}
            {/*                {item.link === "partners" && partnersRequestAmount > 0 &&*/}
            {/*                <h6 className={styles.right_notification}>*/}
            {/*                    {partnersRequestAmount >= 10 ? "9+" : partnersRequestAmount}*/}
            {/*                </h6>}*/}
            {/*            </div>*/}
            {/*        )}*/}
            {/*    </AppWhiteBlock>*/}
            {/*</div>*/}
        </div>
    </RequestView>
}

export default CompanyPage;
