function FactoryInformationPage(
    {
        history,
        match,
    }) {

    const factoryId = match.params.factory;

    const [loading, setLoading] = useState(false);
    // const onSave = (data) => {
    //     history.push(MAIN_URL.ORGANIZATIONS + ORGANIZATIONS_URL.COMPANIES +
    //         `/${data.id}` + COMPANY_URL.INFO)
    // }

    const [data, setData] = useState({})

    const {REQUEST} = RequestWrapper();
    const dispatch = useDispatch();

    const [errors, setErrors] = useState({})
    const [error, setError] = useState("")


    const onUpdateFactory = (body) => {
        dispatch(waitingForUserData())
        REQUEST.POST({
            request: "organizations/factory/update/",
            body: {...body, id: factoryId},
            onSuccess: {message: " !"}
        }).then(res => {
            dispatch(falseLoadingForUserData())
            try {
                console.log("|>>>>> NEW FactoryInfo", res.json)
                if (res.status === 200) {
                    // onSave(res.json)
                    // onSave(body)
                } else {
                    setErrors(res.json)
                }
            } catch (e) {
                setError(":" + e.toString())
                console.log("ERR GET COMPANY:", e);
            }
        })
    };

    const onRequest = (res) => {
        setData(res.json.info);
        setLoading(false);
    }

    return <RequestView
        loading={loading}
        request={`${GET_REQUEST}?id=${factoryId}`}
        onRequest={onRequest}
    >
        <FactoryInformationForm
            onSubmit={onUpdateFactory}
            requestErrors={errors}
            editable={true}
            error={error}
            data={data}
            buttonText={""}
        />
    </RequestView>
}

export default FactoryInformationPage;
