import React, {useCallback, useEffect, useRef, useState} from 'react';
import styles from "./styles.module.scss";
import FormPattern from "../../../containers/Forms/FormPattern";
import {falseLoadingForUserData, loadUserData, waitingForUserData} from "../../../store/actions/user";
import {useDispatch} from "react-redux";
import AppInput from "../../../components/ui/AppInput";
import RequestWrapper from "../../../helpers/RequestWrapper";
import {COMPANY_TYPE_LIST, COMPANY_URL, MAIN_URL, ORGANIZATIONS_URL} from "../../../constants";
import AppSelectedList from "../../../components/ui/AppSelectedList";
import CompanyInformationForm from "../../../containers/Forms/CompanyInformationForm";


function AddCompanyPage({history}) {

    const onSave = (data) => {
        history.push(MAIN_URL.ORGANIZATIONS + ORGANIZATIONS_URL.COMPANIES +
            `/${data.id}` + COMPANY_URL.INFO)
    }

    const {REQUEST} = RequestWrapper();
    const dispatch = useDispatch();

    const [errors, setErrors] = useState({})
    const [error, setError] = useState("")

    const onAddCompany = (body) => {
        dispatch(waitingForUserData())
        REQUEST.POST({
            request: "organizations/company/create/",
            body,
        }).then(res => {
            dispatch(falseLoadingForUserData())
            try {
                console.log("|>>>>> NEW CompanyInfo", res.json)
                if (res.status === 200) {
                    onSave(res.json)
                } else {
                    setErrors(res.json)
                }
            } catch (e) {
                setError(":" + e.toString())
                console.log("ERR GET COMPANY:", e);
            }
        })
    };

    return <CompanyInformationForm
        onSubmit={onAddCompany}
        requestErrors={errors}
        error={error}
    />
}

export default AddCompanyPage;
