import React, {} from "react";
import {Redirect, Route, Switch} from 'react-router-dom';
import {PROFILE_URL, MAIN_URL, ORGANIZATIONS_URL, COMPANY_URL, PRODUCT_URL, SEARCH_URL} from "../constants";
import SearchPage from "../pages/SearchPage";
import ProductShowPage from "../pages/CompaniesPages/ProductsPage/ProductShowPage";
import CreateProductRequestPage from "../pages/RequestPages/CreateProductRequestPage";


const SearchRouter = () => {

    return <Switch>
        <Route exact path={MAIN_URL.SEARCH}
               component={SearchPage}/>
        <Route exact path={
            MAIN_URL.SEARCH + SEARCH_URL.PRODUCT +
            "/:product"
        }
               component={ProductShowPage}
        />
        <Route exact path={
            MAIN_URL.SEARCH + SEARCH_URL.PRODUCT +
            "/:product" + PRODUCT_URL.CREATE_REQUEST
        }
               component={CreateProductRequestPage}
        />
        <Redirect to={MAIN_URL.SEARCH}/>
    </Switch>
};


export default SearchRouter;
