function AppButton(
    {
        onClick = () => {
        },
        className = "styles.empty_class",
        type = "", // green/red/gray
        inverse = false,
        children,
        ...props
    }) {

    return <div>
        <button
            className={cn(styles.bt,
                inverse && styles.inverse_button,
                type === "red" && styles.button_red,
                type === "green" && styles.button_green,
                type === "gray" && styles.button_gray,
            )}
            onClick={() => onClick()}
            type="button"
            {...props}
        >
            {children}
        </button>
    </div>;
}

export default AppButton;
