import React, {useEffect, useState} from "react";
import cn from "classnames";
import styles from "./styles.module.scss";

const CheckNumber = (number, from, to) => {
    let num = Number(number)
    // console.log("NUM", num, isNaN(num))
    if (isNaN(num)) {
        return "  "
    } else {
        if (typeof from !== 'undefined' && num < from) {
            return `    ${from}`
        }
        if (typeof to !== 'undefined' && num > to) {
            return `    ${to}`
        }
    }
    return ""
}

const INPUT_TYPES = {
    NUMBER: "number",
    TEXT: 'text',
}

function AppInput(
    {
        label = "",
        placeholder = "",
        value = "",
        setValue = () => {
        },
        hasErrorFunction = () => {},
        from = undefined,
        to = undefined,
        onChangeValue = undefined,
        onFocus = () => {
        },
        onBlur = () => {
        },
        maxLength = 60,
        error = "",
        type = "text",
        editable = true,
        ...props
    }) {
    // const onChange = editable ? setValue : (e) => {}

    const [totalError, setError] = useState(error)

    useEffect(() => {
        setError(error)
    }, [error])

    useEffect(() => {
        if (totalError) {
            hasErrorFunction(true)
        } else {
            hasErrorFunction(false)
        }
    } , [totalError])

    const onChange = (v) => {
        if (!editable) {
            return;
        }
        if (typeof onChangeValue === 'function') {
            onChangeValue()
        }
        let err = ""
        if (type === INPUT_TYPES.NUMBER) {
            err = CheckNumber(v, from, to)
        } else if (type === INPUT_TYPES.TEXT) {
            err = ""
        }
        // console.log("NEW ERR", type, err)
        setError(err)
        setValue(v)
    }

    const [active, setActive] = useState(false);

    return <div className={styles.container}>
        <label className={
            cn(
                styles.label,
            styles.label_text,
            active && editable ? styles.active : styles.inactive,
            !editable && styles.not_editable,
            totalError && styles.error)}>
            {label}
        </label>
        <div className={cn(
            styles.field_input,
            active && editable ? styles.active : styles.inactive,
            !editable && styles.not_editable,
            totalError && styles.error,
            totalError && styles.error_background,
        )}>
            <input className={
                cn(
                styles.input,
                    !editable && styles.not_editable,
                    totalError && styles.error_background,
                )}
                   value={value}
                   placeholder={placeholder}
                   maxLength={maxLength}
                   type={type}
                   contentEditable={"false"}
                   onChange={e => onChange(e.target.value)}
                   onFocus={(e) => {
                       setActive(true)
                       onFocus(e)
                       // console.log(e)
                   }}
                   onBlur={(e) => {
                       setActive(false);
                       onBlur(e)
                       //console.log("ACTIVE", e)
                   }}
                   {...props}
            />
            <label className={cn(styles.labelError, styles.error)}>
                {totalError}
            </label>
        </div>
    </div>;
}


export default AppInput;
