import React, {useCallback, useEffect, useRef, useState} from 'react';
// import styles from "./styles.module.scss";
import AppInputWrapper from "../../../components/ui/AppInputWrapper";
import {useForm} from "react-hook-form";


function FactoryInformationForm(
    {
        onSubmit = () => {
        },
        editable = true,
        data = undefined,
        requestErrors = {},
        error = "",
        headerText = "  ",
        buttonText = "",
    }) {

    const {
        register, handleSubmit, setError,
        setValue, formState: {errors, isValid, isDirty}
    } = useForm();

    useEffect(() => {
        if (typeof requestErrors === 'object') {
            for (let key in requestErrors) {
                setError(key, {
                    type: 'custom',
                    message: requestErrors[key]
                })
            }
        }
    }, [requestErrors])

    useEffect(() => {
        for (let key in data) {
            setValue(key, data[key]);
        }
    }, [data])

    const onLocalSubmit = useCallback((data) => {
        console.log("LOCAL SUBMIT:", data)
        onSubmit(data)
    }, [onSubmit])

    const [totalError, setTotalError] = useState(error)

    useEffect(() => {
        if (error) {
            setTotalError(error)
            return
        }
        const hasKeys = !!Object.keys(requestErrors).length;
        if (hasKeys) {
            setTotalError("   ")
        } else {
            setTotalError("")
        }
    }, [requestErrors, error])

    const dataArray = [
        {
            name: "name",
            type: "text",
            label: "",
            options: {required: " "},
            placeholder: "  ...",
            error: errors.name,
        },
        {
            name: "email",
            type: "text",
            label: "Email",
            // options: { required: " " },
            placeholder: " email ...",
            error: errors.email,
        },
        {
            name: "address",
            type: "text",
            label: " ",
            placeholder: " ...",
            error: errors.address,
        },
        {
            name: "postal_code",
            type: "text",
            options: {
                valueAsNumber: true,
            },
            label: " ",
            placeholder: " ...",
            error: errors.postal_code,
        },
        {
            name: "website",
            type: "text",
            label: " ",
            placeholder: "https://myfactory.ru...",
            error: errors.website,
        },
    ]

    return <form onSubmit={handleSubmit(onLocalSubmit)}>
        <h4>{headerText}</h4>
        {dataArray.map((
            {
                name,
                label,
                options,
                type = "text",
                placeholder
            }) =>
            <AppInputWrapper
                key={name}
                label={label}
                errors={errors}
                name={name}>
                <input disabled={!editable} {...register(name, options)}
                       placeholder={placeholder} type={type}/>
            </AppInputWrapper>
        )}
        <p style={{margin: 20, }}>
               ,    ,       .
        </p>
        <AppInputWrapper
            label={""}
            errors={errors}
            name={"description"}>
            <textarea disabled={!editable}
                      {...register("description", {})}
                   placeholder={"   ,   ..."}/>
        </AppInputWrapper>
        <input type="submit" value={buttonText}/>
    </form>
}

export default FactoryInformationForm;
