import React, {useCallback, useEffect, useState} from 'react';
import styles from "./styles.module.scss";
import {useDispatch, useSelector} from "react-redux";
import FormPattern from "../../../containers/Forms/FormPattern";
import {loadUserData, waitingForUserData} from "../../../store/actions/user";
import RequestWrapper from "../../../helpers/RequestWrapper";
import AppInput from "../../../components/ui/AppInput";
import AppButton from "../../../components/ui/AppButton";
import {TG_BOT_URL} from "../../../constants";
import {AppWhiteBlock} from "../../../components/ui/AppWhiteBlock";
import AppInputWrapper from "../../../components/ui/AppInputWrapper";


function NotificationsPage() {

    const telephone = useSelector(state => state.user.telephone)
    const telegram_confirmed = useSelector(state => state.user.telegram_confirmed)
    const [telephoneLocal, setTelephoneLocal] = useState("")
    const [errors, setErrors] = useState("")
    const [telephoneError, setTelephoneError] = useState("")

    useEffect(() => {
        setTelephoneLocal(telephone)
    }, [telephone])

    const {REQUEST} = RequestWrapper()
    const dispatch = useDispatch();

    const onSaveUserInfo = () => {
        setTelephoneError("")
        dispatch(waitingForUserData())
        REQUEST.POST({
            request: "users/user/",
            body: {telephone: telephoneLocal}
        }).then(res => {
            try {
                console.log("|>>>>> NEW Profile user info", res.json)
                if (res.status === 200) {
                    dispatch(loadUserData(res.json))
                } else {
                    dispatch(loadUserData({}))
                    setErrors(res.json)
                    try {
                        if (typeof res.json.telephone !== 'undefined') {
                            setTelephoneError(res.json.telephone[0])
                        }
                    } catch (e) {
                        //
                    }
                }
            } catch (e) {
                console.log("ERR GET USER:", e);
            }
        })
    };

    const onGetUserInfo = () => {
        dispatch(waitingForUserData())
        REQUEST.CREATED.GET_USER_INFO()
            .then(res => {
                console.log("Res CHECK first:", res)
                if (res.status === 200) {
                    dispatch(loadUserData(res.json))
                } else {
                    dispatch(loadUserData({}))
                }
            })
    };


    const onOpenTgBot = useCallback(() => {
        window.open(TG_BOT_URL)
    }, [])

    return <div className={styles.screen}>
        {/*<p>ff</p>*/}
        <FormPattern buttonText=""
                     headerText={""}
                     onClick={onSaveUserInfo}
        >
            <AppInputWrapper
                label={" "}
                name={"telephoneError"}
                errors={{telephoneError}}>
                <input value={telephoneLocal}
                       type={"text"}
                       onChange={(e) => setTelephoneLocal(e.target.value)}
                       placeholder={"7-987-654-32-10"}
                    // error={telephoneError}
                    // label={" "}
                />
            </AppInputWrapper>
        </FormPattern>
        <AppWhiteBlock className={styles.white_block}>
            <div>
                {telegram_confirmed ?
                    <h6>
                         Telegram   .
                    </h6> :
                    telephone ?
                        <h6>
                              Telegram  .
                        </h6> :
                        <h6>
                                Telegram    .
                        </h6>
                }
                {telegram_confirmed ?
                    <AppButton onClick={onOpenTgBot}>
                         
                    </AppButton> :
                    telephone ?
                        <div className={styles.buttons}>
                            <div className={styles.button_left}>
                                <AppButton onClick={onOpenTgBot}>
                                     
                                </AppButton>
                            </div>
                            <AppButton onClick={onGetUserInfo}>
                                 
                            </AppButton>
                        </div> :
                        <p className={styles.gray}>
                                  .
                        </p>
                }
            </div>
        </AppWhiteBlock>
    </div>
}

export default NotificationsPage;
