import React, {} from "react";
import {Redirect, Route, Switch} from 'react-router-dom';
import {PROFILE_URL, MAIN_URL, ORGANIZATIONS_URL, COMPANY_URL, PRODUCT_URL} from "../constants";
import CompaniesPage from "../pages/CompaniesPages/CompaniesPage";
import FactoriesPage from "../pages/CompaniesPages/FactoriesPage";
import ProductsPage from "../pages/CompaniesPages/ProductsPage";
import CompanyPage from "../pages/CompaniesPages/CompanyPage";
import AddCompanyPage from "../pages/CompaniesPages/AddCompanyPage";
import CompanyAddUserPage from "../pages/CompaniesPages/CompanyPage/CompanyAddUserPage";
import CompanyAddProductPage from "../pages/CompaniesPages/ProductsPage/CompanyAddProductPage";
import EditProductPage from "../pages/CompaniesPages/ProductsPage/EditProductPage";
import CompanyProductFiles from "../pages/CompaniesPages/ProductsPage/CompanyProductFiles";
import CompanyRouter from "./CompanyRouter";


const CompaniesRouter = () => {

    return <Switch>
        <Route exact path={MAIN_URL.ORGANIZATIONS}
               component={CompaniesPage}/>
        <Route exact path={MAIN_URL.ORGANIZATIONS + ORGANIZATIONS_URL.COMPANIES}
               component={CompaniesPage}/>
        <Route exact path={
            MAIN_URL.ORGANIZATIONS + ORGANIZATIONS_URL.COMPANIES + COMPANY_URL.ADD
        }
               component={AddCompanyPage}/>
        <Route path={
            MAIN_URL.ORGANIZATIONS + ORGANIZATIONS_URL.COMPANIES +
            "/:company" // + COMPANY_URL.INFO
        }
               component={CompanyRouter}/>
        <Route exact path={MAIN_URL.ORGANIZATIONS + ORGANIZATIONS_URL.FACTORIES}
               component={FactoriesPage}/>
        <Route exact path={MAIN_URL.ORGANIZATIONS + ORGANIZATIONS_URL.PRODUCTS}
               component={ProductsPage}/>

        <Redirect to={MAIN_URL.ORGANIZATIONS}/>
    </Switch>
};


export default CompaniesRouter;
