import React, { useState } from "react";
import st from '../styles/Question.module.scss'
import { motion, AnimatePresence } from "framer-motion";


function Question(props) {

    const {
        id,
        title = 'click me',
        children,
    } = props;

    const [isVisible, setVisible] = useState(false);

    const handleVisibility = () => setVisible(!isVisible)

    return (
        <div className={st.question_wrapper}>
            <div onClick={handleVisibility} className={st.question}>
                <div className={st.title}>
                        {title}
                    <AnimatePresence>
                        {!isVisible &&  (
                            <motion.div>
                                <div className={st.disabled}>&#10096;</div>
                            </motion.div>
                        )}
                    </AnimatePresence>
                    <AnimatePresence>
                        { isVisible && (
                            <motion.div>
                                <div className={st.activ}>&#10096;</div>
                            </motion.div>
                        )}
                    </AnimatePresence>
                </div>
                <AnimatePresence>
                    {
                        isVisible && (
                            <motion.div
                                initial={{ height: 0 }}
                                animate={{ height: 'auto' }}
                                exit={{ height: 0 }}
                                style={{ overflow: 'hidden' }}
                                transition={{ duration: 0.6 }}
                            >
                                {children.split('\n').map((item , key) => 
                                <div className={st.description}>
                                    {item}
                                </div>)}
                                {/* <div className={st.description}>
                                    <p> 
                                    {children}
                                    <br/>
                                    dghdghdfg
                                    </p>
                                </div> */}
                            </motion.div>
                        )
                    }
                </AnimatePresence>
            </div >
        </div>
    );
}

export default Question