import React, {useCallback, useEffect, useLayoutEffect, useRef, useState} from 'react';
import styles from "./styles.module.scss";
import {addCompany, falseLoadingForUserData, loadUserData, waitingForUserData} from "../../../../store/actions/user";
import {useDispatch, useSelector} from "react-redux";
import RequestWrapper from "../../../../helpers/RequestWrapper";
import {COMPANY_TYPE_LIST, COMPANY_URL, FACTORY_URL, MAIN_URL, ORGANIZATIONS_URL} from "../../../../constants";
import RequestView from "../../../../components/RequestView";
import {useForm} from "react-hook-form";
import AppInputWrapper from "../../../../components/ui/AppInputWrapper";

const GET_COMPANY_REQUEST = "organizations/company/get/"

function AddFactoryPage(
    {
        history,
        match,
    }
) {

    const companyId = match.params.company;

    const onSave = (data) => {
        history.push(MAIN_URL.ORGANIZATIONS + ORGANIZATIONS_URL.COMPANIES +
            `/${companyId}` + COMPANY_URL.FACTORIES +
            `/${data.id}` + FACTORY_URL.INFO)
    }

    const {REQUEST} = RequestWrapper();
    const dispatch = useDispatch();

    const {register, handleSubmit, setError, setValue,
        formState: {errors}} = useForm();

    // const [errors, setErrors] = useState({})
    // const [error, setTotalError] = useState("")

    const cacheCompanies = useSelector(state => state.user.companies)
    const [needDoRequest, setNeedDoRequest] = useState(
        cacheCompanies.findIndex(i => (i.id === companyId && i.name)) < 0)
    const [loading, setLoading] = useState(true)
    const [companyName, setCompanyName] = useState("")

    useLayoutEffect(() => {
        setNeedDoRequest(cacheCompanies.findIndex(i => (i.id === companyId && i.name)) < 0)
    }, [companyId, cacheCompanies])

    useLayoutEffect(() => {
        if (!needDoRequest) {
            try {
                setCompanyName(cacheCompanies.find(i => i.id === companyId).name)
            } catch (e) {
                //
            }
        }
    }, [needDoRequest])

    useEffect(() => {
        setValue("company", companyName)
    }, [companyName])

    const onCompanyRequest = (res) => {
        // console.log("PRODUCTS::::", res.json)
        // setProducts(res.json.list)
        dispatch(addCompany(res.json.info))
        setCompanyName(res.json.info.name)
        setLoading(false)
    }


    const onAddFactory = (body) => {
        dispatch(waitingForUserData())
        REQUEST.POST({
            request: "organizations/factory/create/",
            body: {
                ...body,
                company: companyId,
            },
        }).then(res => {
            dispatch(falseLoadingForUserData())
            try {
                console.log("|>>>>> NEW CompanyInfo", res.json)
                if (res.status === 200) {
                    onSave(res.json)
                } else {
                    if (res.json.name)
                    setError("name", res.json.name)
                }
            } catch (e) {
                setError(":" + e.toString())
                console.log("ERR ADD FACTORY:", e);
            }
        })
    };

    return <RequestView
        needDoRequest={needDoRequest}
        request={`${GET_COMPANY_REQUEST}?id=${companyId}`}
        loading={loading}
        onRequest={onCompanyRequest}
    >
        <form onSubmit={handleSubmit(onAddFactory)}>
            <h4> </h4>
            <AppInputWrapper
                label={" ()"}
                errors={errors}
                name={"company"}>
                <input type={"text"}
                       {...register("company")}
                       disabled={true}
                       // value={companyName}
                />
            </AppInputWrapper>
            <AppInputWrapper
                label={" "}
                errors={errors}
                name={"name"}>
                <input
                    {...register("name", {required: " "})}
                    placeholder={" ..."}
                    type={"text"}/>
            </AppInputWrapper>
            <input type="submit" value={""}/>
        </form>
    </RequestView>
}

export default AddFactoryPage;
